import * as React from "react";
import classNames from "classnames";

import styles from "./DataTable.module.css";
import { Typography } from "../Typography";
import { ArrowUpDownIcon, SetDownIcon } from "../..";
import { SortDirection } from "./types";

type TableHeaderProps<K> = {
  /**
   * Optional class name for the header
   */
  className?: string;
  /**
   * Column header label
   */
  label: string;
  /**
   * Specify optional percentage width of column
   */
  colWidth?: number;
  /**
   * Is column sortable?
   */
  isSort: boolean;
  /**
   * Sort key of column
   */
  sortKey?: K;
  /**
   * Sort direction of table
   */
  sortDirection?: SortDirection;
  /**
   * Sort key of table
   */
  currentSortKey?: K;
  /**
   * Click handler for sorting
   */
  onClick?: (e: React.MouseEvent<HTMLButtonElement>) => void;
};

type Props<K> = TableHeaderProps<K> & React.ComponentPropsWithRef<"th">;

function TableHeader<K>(props: Props<K>): JSX.Element {
  const { label, className, isSort, sortKey, sortDirection, currentSortKey, colWidth, colSpan, onClick } = props;

  const isSorted = currentSortKey === sortKey;
  const ariaSortDirection = sortDirection === "asc" ? "ascending" : "descending";
  const ariaSort = isSorted ? ariaSortDirection : "none";

  const tableHeaderClassName = classNames(styles.cell, className, {
    [styles.sortable]: isSort,
    [styles.sortActive]: currentSortKey === sortKey,
    [styles.sortAscending]: currentSortKey === sortKey && sortDirection === "asc",
    [styles.sortDescending]: sortKey === sortKey && sortDirection === "desc",
  });

  const tableHeaderStyle = colWidth ? { width: `${colWidth}%` } : undefined;

  return (
    <th style={tableHeaderStyle} className={tableHeaderClassName} aria-sort={ariaSort} colSpan={colSpan} scope="col">
      <button className={styles.tableHeaderButton} onClick={onClick} disabled={!isSort}>
        <div className={styles.label}>
          <Typography className={styles.tableHeaderLabel} variant="small">
            {label}
          </Typography>
          <span className={styles.icon}>
            {isSort && isSorted && <SetDownIcon />}
            {isSort && !isSorted && <ArrowUpDownIcon />}
          </span>
        </div>
      </button>
    </th>
  );
}

export default TableHeader;
