import * as React from "react";
import classNames from "classnames";

import styles from "./MonthPicker.module.css";
import { ALL_MONTHS } from "../DatePicker/util";
import { addYears, endOfYear, setMonth, startOfMonth, startOfYear, subYears } from "date-fns";
import { IconButton } from "../IconButton";
import { ArrowDownIcon, ChevronRightIcon } from "../../icons";
import { DateRange } from "@vkbansal/react-date-primitives";
import { Popover } from "../Popover";
import { YearPicker } from "../YearPicker";

export interface MonthPickerProps {
  className?: string;
  selectedDate?: Date;
  boundingRange?: DateRange;
  onMonthSelect?: (date: Date) => void;
  onYearSelect?: (date: Date) => void;
  hideYearPicker?: boolean;
  isPopover?: boolean;
  onOpenYearPicker?: () => void;
  numYears?: number;
}

export const MonthPicker = React.forwardRef<HTMLDivElement, MonthPickerProps>(function MonthPicker(
  {
    className,
    selectedDate,
    onMonthSelect,
    onYearSelect: onYearSelectProp,
    boundingRange,
    hideYearPicker = false,
    isPopover = false,
    onOpenYearPicker: onOpenYearPickerProp,
    numYears,
  },
  ref
) {
  const [isYearPicker, setIsYearPicker] = React.useState(false);
  const yearPickerButton = React.useRef(null);
  const [currentVisibleDate, setCurrentVisibleDate] = React.useState(selectedDate || new Date());
  const [minDate, maxDate] = boundingRange || [];

  const isInRange = React.useCallback(
    (date: Date) =>
      (minDate ? startOfMonth(date) >= startOfMonth(minDate) : true) &&
      (maxDate ? startOfMonth(date) <= startOfMonth(maxDate) : true),
    [maxDate, minDate]
  );

  const showPrevious = React.useMemo(
    () => isInRange(endOfYear(subYears(currentVisibleDate, 1))),
    [currentVisibleDate, isInRange]
  );

  const showNext = React.useMemo(
    () => isInRange(startOfYear(addYears(currentVisibleDate, 1))),
    [currentVisibleDate, isInRange]
  );

  const onOpenYearPicker = React.useCallback(() => {
    if (isPopover && onOpenYearPickerProp) {
      onOpenYearPickerProp();
    } else {
      setIsYearPicker(true);
    }
  }, [isPopover, onOpenYearPickerProp]);

  const onCloseYearPicker = React.useCallback(() => {
    setIsYearPicker(false);
  }, []);

  const onPreviousClick = React.useCallback(() => {
    const newYear = subYears(currentVisibleDate, 1);
    setCurrentVisibleDate(newYear);
  }, [currentVisibleDate]);

  const onNextClick = React.useCallback(() => {
    const newYear = addYears(currentVisibleDate, 1);
    setCurrentVisibleDate(newYear);
  }, [currentVisibleDate]);

  const onYearSelect = React.useCallback(
    (date: Date) => {
      onYearSelectProp && onYearSelectProp(date);
      onCloseYearPicker();
    },
    [onCloseYearPicker, onYearSelectProp]
  );

  return (
    <div ref={ref} className={classNames(styles.wrapper, className)} data-testid="month-picker">
      <div className={styles.picker}>
        <div className={styles.header}>
          <div className={styles.pickerControl}
                data-testid="month-picker-year-heading">
            {!hideYearPicker ? (
              <button
                className={styles.yearPickerButton}
                data-testid="month-picker-year-button"
                onClick={onOpenYearPicker}
                ref={yearPickerButton}
              >
                {currentVisibleDate.getFullYear()}
                <ArrowDownIcon className={styles.arrowDown} />
              </button>
            ) : (
              currentVisibleDate.getFullYear()
            )}
          </div>
          <div className={styles.controls}>
            {showPrevious && (
              <IconButton
                label="Previous Year"
                data-testid="month-picker-previous-button"
                className={styles.control}
                onClick={onPreviousClick}
              >
                <ChevronRightIcon className={styles.chevronLeft} />
              </IconButton>
            )}
            {showNext && (
              <IconButton
                label="Next Year"
                data-testid="month-picker-next-button"
                className={styles.control}
                onClick={onNextClick}
              >
                <ChevronRightIcon />
              </IconButton>
            )}
          </div>
        </div>
        <div className={styles.months}>
          {ALL_MONTHS.map((month, i) => (
            <div
              data-testid="month-picker-month"
              data-active-month={
                selectedDate && currentVisibleDate.getFullYear() === selectedDate.getFullYear()
                  ? selectedDate.getMonth() === i
                  : false
              }
              data-inrange={isInRange(setMonth(currentVisibleDate, i))}
              className={styles.cell}
              key={i}
              onClick={(): void => {
                onMonthSelect && onMonthSelect(new Date(currentVisibleDate.getFullYear(), i));
              }}
            >
              {month.shortName}
            </div>
          ))}
        </div>
      </div>
      <Popover
        isOpen={isYearPicker}
        onClose={onCloseYearPicker}
        anchorElement={yearPickerButton.current || undefined}
        popperOptions={{
          placement: "bottom-start",
          modifiers: [
            {
              name: "offset",
              options: {
                offset: [-10, -30],
              },
            },
          ],
        }}
      >
        {isYearPicker && (
          <YearPicker
            selectedDate={selectedDate}
            onYearSelect={onYearSelect}
            boundingRange={boundingRange}
            numYears={numYears}
          />
        )}
      </Popover>
    </div>
  );
});
