import * as React from "react";
import { useCombobox } from "downshift";
import classNames from "classnames";
import { TextInput } from "../TextInput";
import { Popover } from "../Popover";
import { SearchIcon } from "../../icons/SearchIcon";
import selectStyles from "../SelectInput/SelectInput.module.css";
import { IconButton } from "../IconButton";
import { SharedSelectProps } from "../SelectInput/SelectInput";
import useContentScrolls from "../../hooks/useContentScrolls";

type AutosuggestItem = {
  id: string;
  label: string;
  value: string;
};

interface AutosuggestProps extends SharedSelectProps {
  items: AutosuggestItem[];
  handleSelectedItemChange?: (selectedItem: AutosuggestItem | null | undefined) => void;
  renderIcon?: (item: AutosuggestItem) => JSX.Element;
}

export const Autosuggest = React.forwardRef<HTMLDivElement, AutosuggestProps>(function Autosuggest(props, ref) {
  const {
    className,
    items,
    handleSelectedItemChange,
    popoverSize = "full",
    placeholder = "Start typing...",
    isSkeleton,
    renderIcon,
    popperOptions = {
      modifiers: [
        {
          name: "offset",
          options: {
            offset: [0, 2],
          },
        },
      ],
    },
    ...otherProps
  } = props;
  const [inputItems, setInputItems] = React.useState(items);
  const [referenceElement, setReferenceElement] = React.useState<HTMLDivElement | null>(null);

  const {
    isOpen,
    openMenu,
    closeMenu,
    highlightedIndex,
    getInputProps,
    getMenuProps,
    getComboboxProps,
    getItemProps,
    getToggleButtonProps,
    selectedItem,
    selectItem,
    setInputValue,
  } = useCombobox<AutosuggestItem | null>({
    items: inputItems,
    itemToString: (item) => (item ? item.label : ""),
    onSelectedItemChange: ({ selectedItem: selected }) => {
      handleSelectedItemChange && handleSelectedItemChange(selected);
      if (selected) {
        closeMenu();
      }
    },
    onInputValueChange: ({ inputValue }) => {
      if (!inputValue) {
        setInputItems(items);
        selectItem(null);
        return;
      }

      if (selectedItem && selectedItem.label !== inputValue) {
        selectItem(null);
        setInputValue(inputValue);
      }

      setInputItems(items.filter((el) => el.label.toLowerCase().startsWith(inputValue.toLowerCase())));
    },
  });


  const {contentScrolls} = useContentScrolls(getMenuProps().ref);

  return (
    <div aria-busy={isSkeleton} ref={ref} className={classNames(selectStyles.container, className)} {...otherProps}>
      <div {...getComboboxProps({ ref: setReferenceElement })}>
        <TextInput
          isSkeleton={isSkeleton}
          placeholder={placeholder}
          {...getInputProps({
            onFocus: openMenu,
          })}
          iconLeftClassName={selectStyles.iconLeft}
          iconLeft={
            <IconButton
              isSkeleton={isSkeleton}
              className={selectStyles.toggleButton}
              type="button"
              label="Icon button"
              {...getToggleButtonProps()}
            >
              <SearchIcon />
            </IconButton>
          }
        />
      </div>
      <Popover
        className={selectStyles.selectInputPopper}
        popoverClassName={selectStyles.selectInputPopover}
        isOpen={isOpen}
        anchorElement={referenceElement || undefined}
        onClose={closeMenu}
        popperOptions={{ placement: "bottom", ...popperOptions }}
        size={popoverSize === "fixed" && contentScrolls ? "large" : popoverSize}
        disableScale
      >
        <ul className={selectStyles.menu} {...getMenuProps()}>
          {inputItems.map((item, index) => (
            <li
              className={classNames(
                selectStyles.item,
                selectStyles.iconItem,
                {
                  [selectStyles.highlight]: highlightedIndex === index,
                },
                {
                  [selectStyles.selected]: selectedItem?.value === item.value,
                }
              )}
              key={`${item.id}`}
              {...getItemProps({ item, index })}
            >
              <div className={classNames(selectStyles.itemContent)}>
                {renderIcon && renderIcon(item)}
                <span className={selectStyles.itemLabel}>{item.label}</span>
              </div>
            </li>
          ))}
        </ul>
      </Popover>
    </div>
  );
});
