import classNames from "classnames";
import * as React from "react";
import { RightArrowIcon } from "../../icons";
import styles from "./DashboardPill.module.css";
import skeletonStyles from "../../theme/skeleton.module.css";

interface DashboardPillStyles extends React.CSSProperties {
  "--color-a": string;
  "--color-b": string;
}

export interface DashboardPillProps {
  className?: string;
  title: string;
  body: string;
  percentage?: number;
  colorA?: string;
  colorB?: string;
  isSkeleton?: boolean;
  onClick?: () => void;
}

type DashboardPillContentProps = Pick<DashboardPillProps, "title" | "percentage" | "body">;

const DashboardPillContent: React.FC<DashboardPillContentProps> = ({ title, percentage, body }) => (
  <div className={styles.content}>
    <header className={styles.header}>
      <h4 className={styles.title}>{title}</h4>
      {percentage !== undefined && (
        <div
          className={classNames(styles.percentage, {
            [styles.up]: percentage >= 0,
            [styles.down]: percentage < 0,
          })}
        >
          {percentage * 100}%
        </div>
      )}
    </header>
    <h2 className={styles.body}>{body}</h2>
    <RightArrowIcon className={styles.arrow} />
  </div>
);

export const DashboardPill = React.forwardRef<HTMLDivElement, DashboardPillProps>(function DashboardPill(props, ref) {
  const { className, title, body, percentage, colorA, colorB, isSkeleton, onClick, ...otherProps } = props;
  const isClickable = onClick !== undefined;
  return (
    <div
      onClick={onClick}
      ref={ref}
      aria-busy={isSkeleton}
      data-testid="dashboard-pill"
      className={classNames(styles.container, className, {
        [skeletonStyles.skeleton]: isSkeleton,
        [styles.gradient]: !isSkeleton,
        [styles.clickable]: !isSkeleton && isClickable,
      })}
      style={
        {
          "--color-a": colorA,
          "--color-b": colorB,
        } as DashboardPillStyles
      }
      {...otherProps}
    >
      {!isSkeleton && <DashboardPillContent title={title} body={body} percentage={percentage} />}
    </div>
  );
});
