import * as React from "react";
import classNames from "classnames";

import styles from "./AnnouncementBanner.module.css";
import { CloseIcon } from "../../icons";
import { IconButton } from "../IconButton";

type AnnouncementBannerVariant = "positive" | "negative" | "warning" | "neutral" | "action";

interface AnnouncementStyle extends React.CSSProperties {
  "--announcement-color": string;
  "--announcement-label-color": string;
  "--announcement-icon-color": string;
}

export interface AnnouncementBannerProps {
  className?: string;
  icon?: React.ReactNode;
  title: React.ReactNode;
  subtitle?: React.ReactNode;
  cta?: React.ReactNode;
  ctaClassName?: string;
  variant: AnnouncementBannerVariant;
  isUrgent?: boolean;
  onClose?: () => void;
}

export const AnnouncementBanner = React.forwardRef<HTMLDivElement, AnnouncementBannerProps>(function AnnouncementBanner(
  props,
  ref
) {
  const { className, icon, title, subtitle, cta, ctaClassName, variant, isUrgent, onClose, ...otherProps } = props;
  const uiColourNames = [variant, !isUrgent && "10"].filter(Boolean).join("-");
  const textColour = !isUrgent || (isUrgent && variant === "warning") ? "black" : "white";
  const iconColour = isUrgent ? textColour : variant;
  const announcementStyle: AnnouncementStyle = {
    "--announcement-color": `var(--color-ui-${uiColourNames})`,
    "--announcement-label-color": `var(--color-grey-${textColour})`,
    "--announcement-icon-color": `var(--color-ui-${iconColour})`,
  };
  return (
    <div
      role="banner"
      style={announcementStyle}
      ref={ref}
      className={classNames(styles.container, className, { [styles.noCloseButton]: !onClose })}
      {...otherProps}
    >
      <div className={styles.titleContainer}>
        <span className={styles.titleIcon}>{icon}</span>
        <span className={styles.title}>{title}</span>
      </div>
      {subtitle && <div className={styles.subtitle}>{subtitle}</div>}
      {cta && <div className={classNames(styles.cta, ctaClassName)}>{cta}</div>}
      {onClose && (
        <IconButton
          className={styles.closeButton}
          style={{ color: textColour }}
          label="Close announcement"
          onClick={onClose}
        >
          <CloseIcon />
        </IconButton>
      )}
    </div>
  );
});
