export type ToastVariant = "info" | "success" | "warning" | "error" | "action";

export type ToastCombination = {
  type: string;
  message: (count: number) => string;
  subTextSeperator?: string;
};

export type ToastInfo = {
  message: string;
  subText?: string;
  ctas?: ToastCTA[];
  variant: ToastVariant;
  combine?: ToastCombination;
  removeDelay?: number;
};

export interface ToastCTA {
  text: string;
  primary?: boolean;
  onClick?: () => boolean;
}

export type ToastItem = ToastInfo & {
  id: string;
  combinationToasts: ToastInfo[];
};

export interface State {
  toasts: ToastItem[];
  heights: Record<string, number>;
}

export enum ActionTypes {
  ADD_TOAST,
  REMOVE_TOAST,
  REGISTER_HEIGHT,
}

export type Action =
  | { type: ActionTypes.ADD_TOAST; payload: ToastItem }
  | { type: ActionTypes.REMOVE_TOAST; payload: string }
  | { type: ActionTypes.REGISTER_HEIGHT; payload: { id: string; height: number } };

export const defaultState: State = {
  toasts: [],
  heights: {},
};

export const reducer = (state: State, action: Action): State => {
  const newHeights = { ...state.heights };
  switch (action.type) {
    case ActionTypes.ADD_TOAST:
      if (action.payload.combine) {
        const existingToast = state.toasts.find((t) => t.combine?.type === action.payload.combine?.type);
        if (existingToast) {
          existingToast.combinationToasts = [...existingToast.combinationToasts, action.payload];
          return {
            ...state,
            toasts: [...state.toasts],
          };
        }
      }
      return {
        ...state,
        toasts: [...state.toasts, action.payload],
      };
    case ActionTypes.REMOVE_TOAST:
      delete newHeights[action.payload];
      return {
        ...state,
        toasts: state.toasts.filter((toast: ToastItem) => toast.id !== action.payload),
        heights: newHeights,
      };
    case ActionTypes.REGISTER_HEIGHT:
      return {
        ...state,
        heights: { ...state.heights, [action.payload.id]: action.payload.height },
      };
    default:
      return state;
  }
};
