import * as React from "react";

import styles from "./DateRangeInput.module.css";
import { ArrowDownIcon, CalendarIcon } from "../../icons";
import useCombinedRefs from "../../hooks/useCombinedRefs";
import { Popover } from "../Popover";
import { DateRangePicker, DateRangePickerProps } from "../DateRangePicker";
import { format } from "date-fns";
import { DateRange } from "@vkbansal/react-date-primitives";
import { TextInput } from "../TextInput";

export type DateRangeInputProps = Omit<DateRangePickerProps, "key" | "ref">;

export const DateRangeInput = React.forwardRef<HTMLInputElement, DateRangeInputProps>(function DateInput(props, ref) {
  const {
    className,
    value,
    startDate,
    boundingRange,
    highlightToday,
    onValueChange,
    onRangeSelected,
    hideMonthPicker,
    hideYearPicker,
    displayMonths,
  } = props;

  const innerRef = React.useRef(null);
  const textInputRef = useCombinedRefs(ref, innerRef);
  const [open, setOpen] = React.useState(false);
  const [thisValue, setThisValue] = React.useState<DateRange | undefined>(value);

  React.useEffect(() => {
    setThisValue(value);
  }, [value]);

  const onThisValueChange = React.useCallback(
    (newValue: DateRange) => {
      if (onValueChange) {
        onValueChange(newValue);
      }
      setThisValue(newValue);
    },
    [onValueChange]
  );

  const onInputFocus = React.useCallback(() => {
    setOpen(true);
  }, []);

  const onClose = React.useCallback(() => {
    setOpen(false);
  }, []);

  const getFormattedValue = () => {
    const firstValue = thisValue && thisValue[0] && format(thisValue[0], "do LLLL Y");
    const secondValue = thisValue && thisValue[1] && format(thisValue[1], "do LLLL Y");

    if (firstValue && !secondValue) return firstValue;
    else if (secondValue && !firstValue) return secondValue;
    else if (firstValue && secondValue) return `${firstValue} - ${secondValue}`;
    return "";
  };

  return (
    <>
      <TextInput
        ref={textInputRef}
        className={className}
        inputClassName={styles.input}
        iconLeft={<CalendarIcon />}
        iconLeftClassName={styles.iconLeft}
        iconRight={<ArrowDownIcon />}
        iconRightClassName={styles.iconRight}
        focusOnControlClick
        onFocus={onInputFocus}
        value={getFormattedValue()}
        readOnly
      />
      <Popover
        isOpen={open}
        onClose={onClose}
        anchorElement={textInputRef.current || undefined}
        size="auto"
        popperOptions={{
          placement: "bottom-start",
        }}
        data-testid="daterangeinput-popover"
        data-is-open={open}
      >
        <DateRangePicker
          value={thisValue}
          startDate={startDate}
          boundingRange={boundingRange}
          highlightToday={highlightToday}
          displayMonths={displayMonths}
          onValueChange={onThisValueChange}
          onRangeSelected={onRangeSelected}
          hideMonthPicker={!open || hideMonthPicker}
          hideYearPicker={!open || hideYearPicker}
        />
      </Popover>
    </>
  );
});
