import * as React from "react";
import classNames from "classnames";

import styles from "./ClipboardCopy.module.css";
import colors from "../../theme/colors.module.css";
import { IconButton } from "../IconButton";
import { CopyIcon } from "../../icons";

export interface ClipboardCopyProps {
  className?: string;
  text: string;
  onCopy?: (text: string) => void;
  onError?: (e: Error) => void;
  /**
   * Skeleton mode
   */
  isSkeleton?: boolean;
  /**
   * Accessiblity label
   */
  label?: string;
}

export const ClipboardCopy = React.forwardRef<HTMLDivElement, ClipboardCopyProps>(function ClipboardCopy(props, ref) {
  const { className, text, onCopy, onError, isSkeleton, label, ...otherProps } = props;
  const [isWriting, setIsWriting] = React.useState(false);

  const handleClick = async () => {
    setIsWriting(true);
    try {
      await navigator.clipboard.writeText(text);
      onCopy && onCopy(text);
    } catch (e) {
      onError && onError(e);
    } finally {
      setIsWriting(false);
    }
  };
  return (
    <div
      ref={ref}
      className={classNames(styles.container, { [styles.skeleton]: isSkeleton }, className)}
      {...otherProps}
    >
      <IconButton
        disabled={isWriting}
        aria-busy={isSkeleton}
        label={label ? label : "Copy to clipboard"}
        className={styles.iconButton}
        onClick={handleClick}
        isSkeleton={isSkeleton}
      >
        <CopyIcon fillColor={colors["color-grey-white"]} />
      </IconButton>
    </div>
  );
});
