import * as React from "react";
import classNames from "classnames";

import styles from "./ProgressBar.module.css";

export interface ProgressBarProps {
  /**
   * Fraction of the width of the bar to be filled.
   * Number should be between 0 and 1.
   */
  progress: number;
  color?: string;
}

type Props = Omit<React.ComponentPropsWithRef<"div">, keyof ProgressBarProps> & ProgressBarProps;

export const ProgressBar = React.forwardRef<HTMLDivElement, Props>(function ProgressBar(props, ref) {
  const { className, progress, color, ...otherProps } = props;

  const fracFilled = Math.min(1, Math.max(0, progress));

  return (
    <div ref={ref} className={classNames(styles.container, className)} style={{ color }} {...otherProps}>
      <div className={styles.progressBar} style={{ width: `${fracFilled * 100}%` }}></div>
    </div>
  );
});
