import classNames from "classnames";
import React, { useMemo } from "react";

import { CircleIcon, CloseIcon } from "../..";
import useContentScrolls from "../../hooks/useContentScrolls";
import { IconButton } from "../IconButton";
import { Typography } from "../Typography";
import { CallToAction, CtaSection } from "./CtaSection";
import styles from "./ModalBody.module.css";

export type ModalBodyProps = React.PropsWithChildren<{
  className?: string;
  title: string;
  subtitle?: string;
  size: "small" | "medium" | "large";
  onClose?: () => void;
  callToAction?: CallToAction;
  includeHeaderDivider?: boolean;
  headerIcon?: React.ComponentType<{ fillColor?: string }>;
  headerIconColor?: string;
  centerAlign?: boolean;
}>;

export const ModalBody = React.forwardRef<HTMLDivElement, ModalBodyProps>(function ModalBody(props, ref) {
  const {
    title,
    subtitle,
    size,
    onClose,
    callToAction,
    includeHeaderDivider,
    headerIcon,
    headerIconColor,
    centerAlign,
    className,
    children,
    ...otherProps
  } = props;

  const hasCta = !!callToAction;
  const showCloseX = !!onClose;

  const headerVariant = useMemo(() => {
    switch (size) {
      case "small":
        return "h3";
      case "medium":
        return "h2";
      case "large":
        return "h1";
    }
  }, [size]);

  const { contentRef, contentScrolls } = useContentScrolls<HTMLDivElement>();

  return (
    <div
      ref={ref}
      className={classNames(className, styles.container, styles[size], {
        [styles.withCta]: hasCta,
        [styles.centerAlign]: centerAlign,
      })}
      {...otherProps}
    >
      {showCloseX && (
        <IconButton onClick={onClose} label="Close" className={styles.closeX}>
          <CloseIcon fillColor="#bbc0e0" />
        </IconButton>
      )}
      <div className={styles.headers}>
        {headerIcon && <CircleIcon icon={headerIcon} className={styles.headerIcon} backgroundColor={headerIconColor} />}
        <div>
          <Typography variant={headerVariant} className={styles.header}>
            {title}
          </Typography>
          {subtitle && (
            <Typography variant="h4" className={styles.subheader}>
              {subtitle}
            </Typography>
          )}
        </div>
      </div>
      {includeHeaderDivider && <div className={styles.divider} />}
      {children && (
        <div
          ref={contentRef}
          className={classNames(styles.content, {
            [styles.scrolls]: contentScrolls,
          })}
        >
          {children}
        </div>
      )}

      {hasCta && callToAction && <CtaSection callToAction={callToAction} />}
    </div>
  );
});
