import * as React from "react";
import classNames from "classnames";
import { addMonths, endOfMonth, startOfMonth, subMonths } from "date-fns";
import { DateRange, DayName, useDateRange } from "@vkbansal/react-date-primitives";

import styles from "./DateRangePicker.module.css";
import { Month } from "../DatePicker";
import { createMonthsRange } from "../DatePicker/util";

export interface DateRangePickerProps {
  className?: string;
  /** The the number of months to display at once */
  displayMonths?: number;
  /** The start date to initialy show the calendar focused on */
  startDate?: Date;
  /** Show or hide the month picker */
  hideMonthPicker?: boolean;
  /** Show or hide the year picker */
  hideYearPicker?: boolean;
  /** The value of the selection */
  value?: DateRange;
  /** Fired when selection changes or is hovered */
  onValueChange?: (value: DateRange) => void;
  /** Fired when a selection has finished */
  onRangeSelected?: (value: DateRange) => void;
  /** Bounding range to allow date picking from */
  boundingRange?: DateRange;
  /** Highlight todays date */
  highlightToday?: boolean;
}

export const DateRangePicker = React.forwardRef<HTMLDivElement, DateRangePickerProps>(function DateRangePicker(
  {
    className,
    displayMonths = 1,
    startDate,
    hideMonthPicker = false,
    hideYearPicker = false,
    value,
    onValueChange: onValueChangeProp,
    onRangeSelected,
    boundingRange,
    highlightToday,
  },
  ref
) {
  const [currentStartDate, setCurrentStartDate] = React.useState(startDate || new Date());
  const dates = React.useMemo(
    () => createMonthsRange(currentStartDate, displayMonths),
    [currentStartDate, displayMonths]
  );
  const { months, range, setRange, setMonths } = useDateRange(dates, {
    weekStartsOn: "MONDAY" as DayName.MONDAY,
    range: value,
  });
  const [isSelectionActive, setSelectionActive] = React.useState(false);
  const [minDate, maxDate] = boundingRange || [];

  React.useEffect(() => {
    value && setRange(value);
  }, [setRange, value]);

  React.useEffect(() => {
    const newMonth = startDate || new Date()
    setCurrentStartDate(newMonth);
    setMonths(createMonthsRange(newMonth, displayMonths));
  }, [displayMonths, setMonths, startDate]);

  const isInRange = React.useCallback(
    (date: Date) => (minDate ? date >= minDate : true) && (maxDate ? date <= maxDate : true),
    [maxDate, minDate]
  );

  const showPrevious = React.useCallback(
    (offset) => isInRange(endOfMonth(subMonths(currentStartDate, 1 - offset))),
    [currentStartDate, isInRange]
  );

  const showNext = React.useCallback(
    (offset) => isInRange(startOfMonth(addMonths(currentStartDate, 1 + offset))),
    [currentStartDate, isInRange]
  );

  const onPreviousClick = React.useCallback(() => {
    const newMonth = subMonths(currentStartDate, 1);
    setCurrentStartDate(newMonth);
    setMonths(createMonthsRange(newMonth, displayMonths));
  }, [currentStartDate, displayMonths, setMonths]);

  const onNextClick = React.useCallback(() => {
    const newMonth = addMonths(currentStartDate, 1);
    setCurrentStartDate(newMonth);
    setMonths(createMonthsRange(newMonth, displayMonths));
  }, [currentStartDate, displayMonths, setMonths]);

  const onMonthSelect = React.useCallback(
    (date: Date) => {
      const newMonth = new Date(currentStartDate);
      newMonth.setMonth(date.getMonth());
      newMonth.setFullYear(date.getFullYear());
      setCurrentStartDate(newMonth);
      setMonths(createMonthsRange(newMonth, displayMonths));
    },
    [currentStartDate, displayMonths, setMonths]
  );

  const onYearSelect = React.useCallback(
    (date: Date) => {
      const newMonth = new Date(currentStartDate);
      newMonth.setFullYear(date.getFullYear());
      setCurrentStartDate(newMonth);
      setMonths(createMonthsRange(newMonth, displayMonths));
    },
    [currentStartDate, displayMonths, setMonths]
  );

  const onValueChange = React.useCallback(
    (newValue: DateRange) => {
      onValueChangeProp && onValueChangeProp(newValue);
      setRange(newValue);
    },
    [onValueChangeProp, setRange]
  );

  return (
    <div className={classNames(styles.rangeWrapper, className)} ref={ref} data-testid="date-range-picker">
      {months.map((rangeMonth, i) => (
        <Month
          type="range"
          key={i}
          hideMonthPicker={hideMonthPicker}
          hideYearPicker={hideYearPicker}
          showPrevious={showPrevious(i)}
          showNext={showNext(i)}
          rangeMonth={rangeMonth}
          setRange={onValueChange}
          onRangeSelect={onRangeSelected}
          range={range}
          boundingRange={boundingRange || [null, null]}
          isSelectionActive={isSelectionActive}
          setSelectionActive={setSelectionActive}
          onPreviousClick={onPreviousClick}
          onNextClick={onNextClick}
          onMonthSelect={onMonthSelect}
          onYearSelect={onYearSelect}
          highlightToday={highlightToday}
        />
      ))}
    </div>
  );
});
