import * as React from "react";
import classNames from "classnames";

import skeletonStyles from "../../theme/skeleton.module.css";

export interface SkeletonProps {
  className?: string;
  width?: string;
  height?: string;
}

export const Skeleton: React.FC<SkeletonProps> = ({ className, width, height, ...otherProps }) => {
  return (
    <div
      aria-busy={true}
      className={classNames(skeletonStyles.skeleton, className)}
      style={{ width, height }}
      {...otherProps}
    />
  );
};
