import React from "react";

import styles from "./ChartLegend.module.css";

export interface ChartLegendProps {
  /**
   * Data series to be displayed in the legend
   */
  data: {
    color: string;
    label: string;
    value: string;
  }[];
  /**
   * Title for the dataset
   */
  title: string;
}

export const ChartLegend: React.FC<ChartLegendProps> = ({ title, data }) => {
  return (
    <table className={styles.container} cellSpacing="0">
      <caption className={styles.title}>{title}</caption>
      <tbody>
        {data.map(({ label, value, color }, i) => (
          <tr key={i}>
            <td className={styles.label}>
              <span className={styles.dot} style={{ backgroundColor: color }} />
              {label}
            </td>
            <td className={styles.value}>{value}</td>
          </tr>
        ))}
      </tbody>
    </table>
  );
};
