import classNames from "classnames";
import * as React from "react";
import styles from "./TextLink.module.css";
import skeletonStyles from "../../theme/skeleton.module.css";

export interface TextLinkProps extends React.ComponentPropsWithRef<"a"> {
  color?: string;
  isSkeleton?: boolean;
}

export const TextLink = React.forwardRef<HTMLAnchorElement, TextLinkProps>(function TextLink(
  { color, children, className, isSkeleton, ...otherProps },
  ref
) {
  return (
    <a
      aria-busy={isSkeleton}
      className={classNames(
        styles.link,
        { [skeletonStyles.skeleton]: isSkeleton, [styles.skeleton]: isSkeleton },
        className
      )}
      style={{ color }}
      ref={ref}
      {...otherProps}
    >
      <span className={styles.linkText}>{children}</span>
    </a>
  );
});

export const textLinkStyles = styles.link;
