import React from "react";

const useContentScrolls = <T extends HTMLElement = HTMLElement>(ref?: React.RefObject<T>): {
  contentScrolls: boolean;
  contentRef: React.RefObject<T>;
} => {
  const [contentScrolls, setContentScrolls] = React.useState(false);
  const contentRef = React.useRef<T>(null);
  const thisRef = ref || contentRef;

  React.useEffect(() => {
    if (thisRef.current) {
      const scrollHeight = thisRef.current.scrollHeight;
      const clientHeight = thisRef.current.clientHeight;

      const scrolls = scrollHeight > clientHeight;

      setContentScrolls(scrolls);
    }
  }, [setContentScrolls, thisRef]);

  return { contentScrolls, contentRef: thisRef };
};

export default useContentScrolls;
