import classNames from "classnames";
import * as React from "react";
import styles from "./CircleIcon.module.css";

export interface CircleIconProps {
  icon: React.ComponentType<{ fillColor?: string }>;
  className?: string;
  backgroundColor?: string;
  color?: string;
}

export const CircleIcon = React.forwardRef<HTMLDivElement, CircleIconProps>(function CircleIcon(
  { icon, backgroundColor, color, className, ...otherProps },
  ref
) {
  const IconComponent = icon;
  return (
    <div className={classNames(styles.circleIcon, className)} style={{ backgroundColor }} ref={ref}>
      <IconComponent fillColor={color ? color : "white"} {...otherProps} />
    </div>
  );
});
