import React, { useLayoutEffect, useState } from "react";
import usePortal from "react-useportal";

import styles from "./FloatingElement.module.css";

export interface FloatingElementProps {
  parent: HTMLElement;
  relativeTo: RelativePosition;
  offset?: Position;
}

type ScreenRect = {
  left: number;
  right: number;
  top: number;
  bottom: number;
};

type Position = {
  x: number;
  y: number;
};

export enum RelativePosition {
  TopLeft = "TopLeft",
  TopRight = "TopRight",
  BottomLeft = "BottomLeft",
  BottomRight = "BottomRight",
}

const getScreenRectFromBoundingRect = (rect: DOMRect): ScreenRect => {
  return {
    left: rect.left + window.scrollX,
    right: rect.right + window.scrollX,
    top: rect.top + window.scrollY,
    bottom: rect.bottom + window.scrollY,
  };
};

const calculateRootPosition = (parentPosition: ScreenRect, cornerPosition: RelativePosition): Position => {
  switch (cornerPosition) {
    case RelativePosition.TopLeft:
      return { x: parentPosition.left, y: parentPosition.top };
    case RelativePosition.TopRight:
      return { x: parentPosition.right, y: parentPosition.top };
    case RelativePosition.BottomLeft:
      return { x: parentPosition.left, y: parentPosition.bottom };
    case RelativePosition.BottomRight:
      return { x: parentPosition.right, y: parentPosition.bottom };
  }
};

export const FloatingElement: React.FC<React.PropsWithChildren<FloatingElementProps>> = ({
  parent,
  relativeTo,
  offset = { x: 0, y: 0 },
  children,
}) => {
  const { Portal } = usePortal();
  const [rootPosition, setRootPosition] = useState<Position>({ x: 0, y: 0 });

  useLayoutEffect(() => {
    function handleResize() {
      if (!parent) return;
      const parentRect = getScreenRectFromBoundingRect(parent.getBoundingClientRect());
      const position = calculateRootPosition(parentRect, relativeTo);
      setRootPosition(position);
    }

    window.addEventListener("resize", handleResize);
    handleResize();
  }, [parent, relativeTo]);

  return (
    <Portal>
      <span
        role="tooltip"
        className={styles.container}
        style={{
          left: `${rootPosition.x}px`,
          top: `${rootPosition.y}px`,
          marginLeft: `${offset.x}px`,
          marginTop: `${offset.y}px`,
        }}
      >
        {children}
      </span>
    </Portal>
  );
};
