import React, { useMemo, useCallback, useRef, useLayoutEffect, useState, useEffect } from "react";
import classNames from "classnames";
import styles from "./Toast.module.css";
import { CloseIcon } from "../../icons/CloseIcon";
import type { ToastCombination, ToastCTA, ToastInfo, ToastVariant } from "./Toast.reducer";
import { Typography } from "../Typography";
import { InfoIcon } from "../../icons";
import { textLinkStyles } from "../TextLink";
import { animated, SpringValue } from "react-spring";

import { useToast } from "./ToastProvider";

type ToastSize = "small" | "medium" | "large";

export interface ToastProps {
  message: string;
  subText?: string;
  ctas?: ToastCTA[];
  combination?: ToastCombination;
  combinations?: ToastInfo[];
  removeDelay?: number;
  id: string;
  removeToast: (id: string) => void;
  variant?: ToastVariant;
  className?: string;
  contentClassName?: string;
  stackHeight: number;
  removed?: boolean;
  transition: { transform: SpringValue<string> };
}

export function Toast({
  className,
  contentClassName,
  message,
  id,
  subText,
  ctas,
  removeDelay: removeDelayProp,
  combination,
  combinations,
  removeToast,
  variant = "info",
  transition,
  stackHeight,
  removed,
}: React.PropsWithChildren<ToastProps>): JSX.Element {
  const { registerHeight } = useToast();
  const toastRef = useRef<HTMLDivElement>(null);
  const [removeCallback, setRemoveCallback] = useState<number | undefined>();
  const prevRemoveCallback = useRef<number>();
  const [constantStackHeight, setConstantStackHeight] = useState(stackHeight);

  const removeDelay = useMemo(() => {
    if (removeDelayProp) return removeDelayProp;

    return ctas?.length ? 6000 : 4000;
  }, [ctas?.length, removeDelayProp]);

  useEffect(() => {
    if (!removed) {
      setConstantStackHeight(stackHeight);
    }
  }, [stackHeight, removed]);

  useLayoutEffect(() => {
    if (toastRef.current) {
      const rect = toastRef.current.getBoundingClientRect();
      registerHeight(id, rect.bottom - rect.top + 16);
    }
    // This ensure this is only ever run once to avoid multiplying heights
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (!ctas?.length || (!ctas?.length && combinations?.length)) {
      const timeout = window.setTimeout(() => {
        removeToast(id);
      }, removeDelay);
      setRemoveCallback(timeout);
      return () => window.clearTimeout(timeout);
    }
  }, [id, ctas?.length, removeToast, combinations?.length, removeDelay]);

  useEffect(() => {
    if (prevRemoveCallback.current) {
      window.clearTimeout(prevRemoveCallback.current);
    }
    prevRemoveCallback.current = removeCallback;
  }, [removeCallback]);

  const size = useMemo<ToastSize>(() => {
    let accumSize = 1;
    if (subText) accumSize += 1;
    if (ctas?.length) accumSize += 1;
    switch (accumSize) {
      case 3:
        return "large";
      case 2:
        return "medium";
      default:
        return "small";
    }
  }, [ctas?.length, subText]);

  const onCtaClick = useCallback(
    (cb?: () => boolean) => {
      if (!cb || (cb && cb())) {
        removeToast(id);
      }
    },
    [id, removeToast]
  );

  const onMouseEnter = useCallback(() => {
    window.clearTimeout(removeCallback)
  }, [removeCallback])

  const onMouseLeave = useCallback(() => {
    const timeout = window.setTimeout(() => {
      removeToast(id);
    }, removeDelay);
    setRemoveCallback(timeout);
  }, [id, removeDelay, removeToast])

  return (
    <div
      className={classNames(styles.toastWrapper, className, styles[size], styles[variant])}
      style={{
        transform: `translateY(${constantStackHeight}px)`,
      }}
      ref={toastRef}
    >
      <animated.div className={styles.toast} style={transition} role="alert">
        <div className={classNames(styles.content, contentClassName)} onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave}>
          <div className={styles.body}>
            <InfoIcon className={styles.infoIcon} />
            <strong className={styles.title}>
              {combination && combinations?.length ? combination.message(combinations.length + 1) : message}
            </strong>
            {subText && (
              <Typography variant="small" className={styles.subText}>
                {combination && combinations?.length
                  ? [subText, ...combinations.map((c) => c.subText)].join(combination.subTextSeperator || " & ")
                  : subText}
              </Typography>
            )}
            {ctas?.length && (
              <div className={styles.ctasContainer}>
                {ctas.map((cta, ctaIndex) => (
                  <button
                    key={ctaIndex}
                    className={classNames(textLinkStyles, styles.cta, { [styles.primary]: cta.primary })}
                    onClick={() => onCtaClick(cta.onClick)}
                  >
                    {cta.text}
                  </button>
                ))}
              </div>
            )}
          </div>
        </div>
        <span data-testid="toast-close-btn" onClick={() => removeToast(id)} className={styles.icon}>
          <CloseIcon />
        </span>
      </animated.div>
    </div>
  );
}
