import * as React from "react";
import { ResponsiveLine, LineSvgProps, PointTooltipProps } from "@nivo/line";
import classNames from "classnames";

import styles from "./LineChart.module.css";
import ChartTooltip from "../ChartTooltip";
import type { LineChartTooltipProps } from "../ChartTooltip";
import { colors } from "../..";

type Props = Pick<LineChartTooltipProps, "formatTooltipItem"> & LineSvgProps;

export interface LineChartProps extends Props {
  className?: string;
  showTooltip?: boolean;
}

export const LineChart = React.forwardRef<HTMLDivElement, LineChartProps>(function LineChart(props, ref) {
  const { data, formatTooltipItem, showTooltip = false, className, ...lineSvgProps } = props;
  return (
    <div ref={ref} className={classNames(styles.container, className)}>
      <ResponsiveLine
        data={data}
        colors={{ datum: "color" }}
        theme={{
          crosshair: {
            line: {
              stroke: colors["color-grey-slate"],
              strokeWidth: 1,
              strokeOpacity: 0.4,
            },
          },
          axis: {
            ticks: {
              text: {
                fill: colors["color-grey-slate"],
                fontFamily: "Axiforma",
                fontSize: "0.75rem",
                opacity: 0.6,
                letterSpacing: 0,
                lineHeight: 0.75,
              },
            },
          },
        }}
        margin={{ top: 16, right: 64, bottom: 48, left: 72 }}
        xScale={{ type: "point" }}
        yScale={{ type: "linear", min: 0, max: "auto", stacked: false, reverse: false }}
        yFormat=" >-.2f"
        axisBottom={{
          tickPadding: 30,
          tickSize: 0,
        }}
        axisLeft={{
          tickPadding: 32,
          tickValues: 5,
          tickSize: 0,
        }}
        enablePoints={false}
        enableGridX={false}
        enableGridY={false}
        enableSlices={false}
        enableCrosshair={true}
        crosshairType="cross"
        useMesh={true}
        lineWidth={3}
        isInteractive={showTooltip}
        tooltip={
          showTooltip
            ? (tooltipProps: PointTooltipProps) => (
                <ChartTooltip.Line {...tooltipProps} formatTooltipItem={formatTooltipItem} data={data} />
              )
            : undefined
        }
        {...lineSvgProps}
      />
    </div>
  );
});
