import classNames from "classnames";
import * as React from "react";
import { usePopper } from "react-popper";
import { Placement } from "@popperjs/core";
import styles from "./Tooltip.module.css";

type TooltipVariant = "default" | "strong";

export interface TooltipProps extends React.ComponentPropsWithRef<"div"> {
  isOpen: boolean;
  variant?: TooltipVariant;
  anchorElement?: HTMLElement;
  distance?: number;
  placement?: Placement;
  className?: string;
  onEnter?: () => void;
  onLeave?: () => void;
}

interface TooltipContentProps {
  title?: React.ReactNode;
}

export const TooltipContent: React.FC<TooltipContentProps> = ({ children, title }) => (
  <div className={styles.content}>
    {title && <div className={styles.title}>{title}</div>}
    {children && <div className={styles.body}>{children}</div>}
  </div>
);

export const Tooltip: React.FC<TooltipProps> = (props) => {
  const [isHover, setHover] = React.useState(false);
  const [arrowElement, setArrowElement] = React.useState<HTMLDivElement | null>(null);
  const {
    isOpen,
    anchorElement,
    children,
    variant = "default",
    distance = 8,
    placement = "right",
    className,
    ...otherProps
  } = props;
  const [popperElement, setPopperElement] = React.useState<HTMLDivElement | null>(null);

  const handleMouseEnter = () => {
    setHover(true);
  };

  const handleMouseLeave = () => {
    setHover(false);
  };

  const { styles: popperStyles, attributes } = usePopper(anchorElement, popperElement, {
    modifiers: [
      {
        name: "offset",
        options: {
          offset: [0, distance],
        },
      },
      { name: "arrow", options: { element: arrowElement } },
    ],
    placement,
  });

  return (
    <div
      role="tooltip"
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
      className={classNames(styles.tooltip, styles[variant], className, {
        [styles.open]: isOpen || isHover,
        [styles.hover]: isHover,
      })}
      ref={setPopperElement}
      style={popperStyles.popper}
      {...attributes.popper}
      {...otherProps}
    >
      <div className={styles.arrow} ref={setArrowElement} style={popperStyles.arrow} />
      {children}
    </div>
  );
};
