import * as React from "react";

import { ArrowDownIcon, CalendarIcon } from "../../icons";
import useCombinedRefs from "../../hooks/useCombinedRefs";
import { Popover } from "../Popover";
import { DatePicker, DatePickerProps } from "../DatePicker";
import { format } from "date-fns";
import { TextInput } from "../TextInput";

import styles from "./DateInput.module.css";

export type DateInputProps = Omit<DatePickerProps, "key" | "ref">;

export const DateInput = React.forwardRef<HTMLInputElement, DateInputProps>(function DateInput(props, ref) {
  const {
    className,
    value,
    startDate,
    boundingRange,
    highlightToday,
    onValueChange: onValueChangeProp,
    hideMonthPicker,
    hideYearPicker,
  } = props;

  const innerRef = React.useRef(null);
  const textInputRef = useCombinedRefs(ref, innerRef);
  const [open, setOpen] = React.useState(false);
  const [thisValue, setThisValue] = React.useState<Date | undefined>(value);

  const resolvedValue = React.useMemo(() => value || thisValue, [thisValue, value]);

  const onValueChange = React.useCallback(
    (newValue: Date) => {
      if (onValueChangeProp) {
        onValueChangeProp(newValue);
      }
      if (!value) {
        setThisValue(newValue);
      }
    },
    [onValueChangeProp, value]
  );

  const onInputFocus = React.useCallback(() => {
    setOpen(true);
  }, []);

  const onClose = React.useCallback(() => {
    setOpen(false);
  }, []);

  return (
    <>
      <TextInput
        ref={textInputRef}
        className={className}
        inputClassName={styles.input}
        iconLeft={<CalendarIcon />}
        iconLeftClassName={styles.iconLeft}
        iconRight={<ArrowDownIcon />}
        iconRightClassName={styles.iconRight}
        focusOnControlClick
        onFocus={onInputFocus}
        value={resolvedValue && format(resolvedValue, "do LLLL Y")}
        readOnly
      />
      <Popover
        isOpen={open}
        onClose={onClose}
        anchorElement={textInputRef.current || undefined}
        size="auto"
        popperOptions={{
          placement: "bottom-start",
        }}
        data-testid="dateinput-popover"
        data-is-open={open}
      >
        <DatePicker
          value={resolvedValue}
          startDate={startDate}
          boundingRange={boundingRange}
          highlightToday={highlightToday}
          onValueChange={onValueChange}
          hideMonthPicker={!open || hideMonthPicker}
          hideYearPicker={!open || hideYearPicker}
        />
      </Popover>
    </>
  );
});
