import * as React from "react";
import classNames from "classnames";
import styles from "./Grid.module.css";

export interface ColProps {
  className?: string;
  size?: number;
  offset?: number;
}

export const Col: React.FC<ColProps> = ({ className, offset, size, children, ...otherProps }) => {
  return (
    <div
      className={classNames(styles.col, className, {
        [styles[`col-${size}`]]: !!size,
        [styles[`offset-${offset}`]]: !!offset,
        [styles.grow]: !size,
      })}
      {...otherProps}
    >
      {children}
    </div>
  );
};
