function getFlagImage(country: string): string | undefined {
  if (country in imageMap) {
    return imageMap[country as keyof typeof imageMap] as string;
  } else if (country in countriesByAlpha2) {
    return imageMap[countriesByAlpha2[country].flagName as keyof typeof imageMap] as string;
  } else if (country in countriesByAlpha3) {
    return imageMap[countriesByAlpha3[country].flagName as keyof typeof imageMap] as string;
  }
}

export interface FlagIconProps {
  className?: string;
  country: string;
  isSkeleton?: boolean;
}

interface FlagImageProps {
  src: string;
  className?: string;
  error: boolean;
  isSkeleton?: boolean;
}

export const FlagImage: React.FC<FlagImageProps> = ({ className, isSkeleton, src, error }) =>
  !isSkeleton && src && !error ? (
    <img className={classNames(styles.flag, className)} src={src} alt="Flag icon" />
  ) : (
    <div
      data-testid="flag-skeleton"
      className={classNames(skeletonStyles.skeleton, styles.flag, styles.skeleton, className)}
    />
  );

export const FlagIcon: React.FC<FlagIconProps> = ({ className, isSkeleton, country }) => {
  const [imageSrc, setImageSrc] = React.useState("");
  const [imageError, setImageError] = React.useState(false);

  React.useEffect(() => {
    setImageError(false);

    const image = new Image();
    const domImageSrc = getFlagImage(country);

    if (domImageSrc) {
      image.src = domImageSrc;
    }

    image.onload = () => {
      setImageSrc(image.src);
    };
    image.onerror = () => {
      setImageError(true);
    };
    if (image.complete) {
      setImageSrc(image.src);
    }
    return () => {
      image.onload = null;
      image.onerror = null;
    };
  }, [country]);

  return <FlagImage className={className} isSkeleton={isSkeleton} src={imageSrc} error={!!imageError} />;
};
