import * as React from "react";
import classNames from "classnames";

import styles from "./Tab.module.css";

export interface TabProps extends React.ComponentPropsWithRef<"button"> {
  className?: string;
  /**
   * Specify a label to identify tab list
   */
  label: string;
  /**
   * Is this tab currently selected
   */
  isSelected?: boolean;
  /**
   * Handler for changing tab value
   * @param e React mouse event
   * @param value Value of new tab
   */
  onTabChange?(e: React.MouseEvent<HTMLButtonElement>, value: string): void;
}

export function Tab(props: TabProps): JSX.Element {
  const { className, label, id, isSelected, onTabChange, ...otherProps } = props;

  const handleClick = (e: React.MouseEvent<HTMLButtonElement>) => {
    if (onTabChange && id) {
      onTabChange(e, id);
    }
  };

  return (
    <button
      className={classNames(styles.button, { [styles.selected]: isSelected }, className)}
      id={id}
      role="tab"
      aria-selected={isSelected}
      onClick={handleClick}
      {...otherProps}
    >
      <span className={styles.label}>{label}</span>
    </button>
  );
}
