import React, { useCallback, useRef, useState } from "react";
import { RangeMonth, DateRange, DayOfMonth, DayOfRangeMonth } from "@vkbansal/react-date-primitives";
import { isFirstDayOfMonth, isLastDayOfMonth, isSameDay, isWithinInterval, maxTime, minTime } from "date-fns";

import styles from "./Month.module.css";
import { ArrowDownIcon, ChevronRightIcon } from "../../icons";
import { IconButton } from "../IconButton";
import { MONTH_NAMES } from "./util";
import { Popover } from "../Popover";
import { MonthPicker } from "../MonthPicker";
import { YearPicker } from "../YearPicker";

export interface BaseMonthProps {
  /** The bounding range */
  boundingRange: DateRange;
  /** Fired when the previous month arrow is clicked */
  onPreviousClick(): void;
  /** Fired when the next month arrow is clicked */
  onNextClick(): void;
  /** Fired when a month is selected */
  onMonthSelect?(date: Date): void;
  /** Fired when a year is selected */
  onYearSelect?(date: Date): void;
  /** Show or hide the month picker */
  hideMonthPicker?: boolean;
  /** Show or hide the year picker */
  hideYearPicker?: boolean;
  /** Show or hide the previous month arrow */
  showPrevious?: boolean;
  /** Show or hide the next month arrow */
  showNext?: boolean;
  /** The number of years to show in the year picker */
  numYears?: number;
  /** Highlight todays date */
  highlightToday?: boolean;
}

export interface RangeMonthProps extends BaseMonthProps {
  type: "range";
  /** The current display month */
  rangeMonth: RangeMonth;
  /** Fired when the range is selected or hovered */
  setRange(range: DateRange): void;
  /** Fired when a selection has finished */
  onRangeSelect?(range: DateRange): void;
  /** The current selected range */
  range: DateRange;
  /** Is a selection currently active */
  isSelectionActive: boolean;
  /** Sets if the selection is active */
  setSelectionActive(status: boolean): void;
}

export interface SingleMonthProps extends BaseMonthProps {
  type: "single";
  /** The current display month */
  month: Date;
  /** The selected value */
  value?: Date;
  /** The days in the current month */
  days: DayOfMonth[];
  /** called when a date is selected */
  setDate(date: Date): void;
}

export type MonthProps = RangeMonthProps | SingleMonthProps;

export const Month = React.forwardRef<HTMLDivElement, MonthProps>(function Month(props, ref) {
  const {
    boundingRange,
    onPreviousClick,
    onNextClick,
    onMonthSelect: onMonthSelectProp,
    onYearSelect: onYearSelectProp,
    hideMonthPicker = false,
    hideYearPicker = false,
    showNext,
    showPrevious,
    numYears,
    highlightToday,
  } = props;
  const [visiblePicker, setVisiblePicker] = useState<"month" | "year" | undefined>(undefined);
  const nextPickerButton = useRef(null);

  const onOpenNextPicker = useCallback(() => {
    if (!hideMonthPicker && visiblePicker !== "month") {
      setVisiblePicker("month");
    } else if (!hideYearPicker && visiblePicker !== "year") {
      setVisiblePicker("year");
    }
  }, [hideMonthPicker, hideYearPicker, visiblePicker]);

  const onCloseNextPicker = useCallback(() => {
    if (!hideMonthPicker && visiblePicker === "month") {
      setVisiblePicker(undefined);
    } else if (!hideMonthPicker && visiblePicker === "year") {
      setVisiblePicker("month");
    } else {
      setVisiblePicker(undefined);
    }
  }, [hideMonthPicker, visiblePicker]);

  const onMonthSelect = useCallback(
    (date: Date) => {
      onMonthSelectProp && onMonthSelectProp(date);
      onCloseNextPicker();
    },
    [onCloseNextPicker, onMonthSelectProp]
  );

  const onYearSelect = useCallback(
    (date: Date) => {
      onYearSelectProp && onYearSelectProp(date);
      onCloseNextPicker();
    },
    [onCloseNextPicker, onYearSelectProp]
  );

  const { days, month } = props.type === "range" ? props.rangeMonth : { days: props.days, month: props.month };
  const [startDate] = props.type === "range" ? props.range : [props.month];
  const monthName = MONTH_NAMES[month.getMonth()];

  const headerText = `${monthName} ${month.getFullYear()}`;

  const isDayOfRangeMonth = (thisDay: DayOfMonth | DayOfRangeMonth): thisDay is DayOfRangeMonth => {
    return (thisDay as DayOfRangeMonth).inRange !== undefined;
  };

  return (
    <div className={styles.month} ref={ref}>
      <div className={styles.header}>
        <div className={styles.monthControl} data-testid="month-month-heading">
          {!hideMonthPicker || !hideYearPicker ? (
            <button
              className={styles.monthPickerButton}
              data-testid="month-month-button"
              onClick={onOpenNextPicker}
              ref={nextPickerButton}
            >
              {headerText}
              <ArrowDownIcon className={styles.arrowDown} />
            </button>
          ) : (
            headerText
          )}
        </div>
        <div className={styles.controls}>
          {showPrevious && (
            <IconButton
              label="Previous Month"
              data-testid="month-previous-button"
              className={styles.control}
              onClick={onPreviousClick}
            >
              <ChevronRightIcon className={styles.chevronLeft} />
            </IconButton>
          )}
          {showNext && (
            <IconButton
              label="Next Month"
              data-testid="month-next-button"
              className={styles.control}
              onClick={onNextClick}
            >
              <ChevronRightIcon />
            </IconButton>
          )}
        </div>
      </div>
      <div className={styles.dayNames}>
        <div className={styles.cell}>Mo</div>
        <div className={styles.cell}>Tu</div>
        <div className={styles.cell}>We</div>
        <div className={styles.cell}>Th</div>
        <div className={styles.cell}>Fr</div>
        <div className={styles.cell}>Sa</div>
        <div className={styles.cell}>Su</div>
      </div>
      <div className={styles.days}>
        {days.map((day, i) => (
          <div
            data-testid={`month-day-${day.inCurrentMonth ? "active" : "inactive"}-${day.dateObj.getDate()}`}
            data-active-month={day.inCurrentMonth}
            data-outside-bounding-range={
              !isWithinInterval(day.dateObj, {
                start: boundingRange[0] || minTime,
                end: boundingRange[1] || maxTime,
              })
            }
            data-inrange={isDayOfRangeMonth(day) ? day.inRange : false}
            data-start={isDayOfRangeMonth(day) ? day.isStart : false}
            data-selected={
              isDayOfRangeMonth(day) ? false : isSameDay(day.dateObj, (props as SingleMonthProps).value || 0)
            }
            data-end={isDayOfRangeMonth(day) ? day.isEnd : false}
            data-week-start={day.dayName === "MONDAY"}
            data-week-end={day.dayName === "SUNDAY"}
            data-start-of-month={isFirstDayOfMonth(day.dateObj)}
            data-end-of-month={isLastDayOfMonth(day.dateObj)}
            data-highlight={highlightToday ? isSameDay(day.dateObj, new Date()) : false}
            className={styles.cell}
            key={i}
            onClick={(): void => {
              if (props.type === "range") {
                if (startDate && props.isSelectionActive && day.dateObj >= startDate) {
                  props.setRange([startDate, day.dateObj]);
                  props.setSelectionActive(false);
                  props.onRangeSelect && props.onRangeSelect([startDate, day.dateObj]);
                } else {
                  props.setRange([day.dateObj, day.dateObj]);
                  props.setSelectionActive(true);
                }
              } else {
                props.setDate(day.dateObj);
              }
            }}
            onMouseEnter={() => {
              if (props.type === "range") {
                if (startDate && props.isSelectionActive && day.dateObj >= startDate) {
                  props.setRange([startDate, day.dateObj]);
                }
              }
            }}
          >
            {day.dateObj.getDate()}
          </div>
        ))}
      </div>
      {hideMonthPicker && hideYearPicker ? null : (
        <Popover
          isOpen={!!visiblePicker}
          popoverClassName={styles.popover}
          onClose={onCloseNextPicker}
          anchorElement={nextPickerButton.current || undefined}
          popperOptions={{
            placement: "bottom-start",
            modifiers: [
              {
                name: "offset",
                options: {
                  offset: [-10, -30],
                },
              },
            ],
          }}
        >
          {visiblePicker === "year" && (
            <YearPicker
              selectedDate={month}
              onYearSelect={onYearSelect}
              boundingRange={boundingRange}
              numYears={numYears}
            />
          )}
          {visiblePicker === "month" && (
            <MonthPicker
              selectedDate={month}
              onMonthSelect={onMonthSelect}
              onYearSelect={onYearSelect}
              onOpenYearPicker={onOpenNextPicker}
              boundingRange={boundingRange}
              hideYearPicker={hideYearPicker}
              isPopover
              numYears={numYears}
            />
          )}
        </Popover>
      )}
    </div>
  );
});
