import * as React from "react";
import classNames from "classnames";

import styles from "./Pagination.module.css";
import { IconButton } from "../IconButton";
import { Typography } from "../Typography";
import { ArrowThinRightIcon } from "../../icons";
import { SelectInput, SelectInputValue } from "../SelectInput";

export interface PaginationContentProps {
  className?: string;
  total: number;
  pageIndex: number;
  perPage: number;
  perPageOptions: string[];
  onSelectPerPage: (perPage: string) => void;
  onPrev: () => void;
  onNext: () => void;
  /**
   * Skeleton mode
   */
  isSkeleton?: boolean;
  /**
   * Pagination accessibility label
   */
  label?: string;
}

export const PaginationContent: React.FC<PaginationContentProps> = ({
  pageIndex,
  total,
  perPage,
  perPageOptions,
  onSelectPerPage,
  onNext,
  onPrev,
  className,
  label,
  isSkeleton,
  ...otherProps
}) => {
  const start = (pageIndex - 1) * perPage;
  const end = Math.min(total, start + perPage);
  const selectOptions = perPageOptions.map<SelectInputValue>((option) => ({ label: option, value: option }));

  return (
    <div
      role="navigation"
      aria-busy={isSkeleton}
      aria-label={label ? label : "Pagination"}
      className={classNames(styles.container, className)}
      {...otherProps}
    >
      <div className={styles.count}>
        <Typography isSkeleton={isSkeleton} variant="small">
          {start + 1} - {end} of {total}
        </Typography>
      </div>
      <IconButton hasBorder isSkeleton={isSkeleton} label="Previous page" onClick={() => onPrev()}>
        <ArrowThinRightIcon isFlip={true} />
      </IconButton>
      <SelectInput
        id="select-pagination-per-page"
        items={selectOptions}
        variant="small"
        aria-label="Select per page"
        initialSelectedItem={{ label: perPage + "", value: perPage + "" }}
        handleSelectedItemChange={(selectedItem) => selectedItem && onSelectPerPage(selectedItem.value)}
        popperOptions={{
          modifiers: [
            {
              name: "offset",
              options: {
                offset: [0, 8],
              },
            },
          ]
        }}
        isSkeleton={isSkeleton}
      />
      <IconButton hasBorder isSkeleton={isSkeleton} label="Next page" onClick={() => onNext()}>
        <ArrowThinRightIcon />
      </IconButton>
    </div>
  );
};
