import * as React from "react";
import classNames from "classnames";
import { ResponsiveChoropleth, ChoroplethCommonProps, ChoroplethBoundFeature } from "@nivo/geo";

import styles from "./ChoroplethChart.module.css";
import ChartTooltip from "../ChartTooltip";
import { colors } from "../..";

type Props = Omit<ChoroplethCommonProps, "features" | "data" | "domain">;

type ChartData = {
  countryCode: string;
  value: number;
};

export interface ChoroplethChartProps extends Props {
  className?: string;
  /**
   * Enables interactivity and shows a contextual tooltip on hover of a country.
   */
  showTooltip?: boolean;
  /**
   * Whether or not to show absolute or formatted values on the tooltip.
   */
  showAbsoluteTooltip?: boolean;
  /**
   * Whether or not to show the color legend.
   */
  showLegend?: boolean;
  /**
   * The GeoJSON object to build the chart data from.
   */
  geoJson: Record<string, unknown>;
  /**
   * The chart data.
   */
  data: ChartData[];
  /**
   * The minimum value of the color range.
   */
  min: number;
  /**
   * The maximum value of the color range.
   */
  max: number;
}

export const ChoroplethChart = React.forwardRef<HTMLDivElement, ChoroplethChartProps>(function ChoroplethChart(
  props,
  ref
) {
  const {
    geoJson,
    data,
    min,
    max,
    showTooltip = false,
    showLegend = true,
    showAbsoluteTooltip = true,
    className,
    ...otherProps
  } = props;

  const features = React.useMemo(() => {
    if (geoJson.type === "FeatureCollection") {
      return geoJson.features as any[];
    } else {
      throw new Error("Provided geoJson is not in expected format");
    }
  }, [geoJson]);

  const chartData = React.useMemo(() => {
    return data.map((v) => ({ id: v.countryCode, value: v.value }));
  }, [data]);

  return (
    <div ref={ref} className={classNames(styles.container, className)}>
      <ResponsiveChoropleth
        data={chartData}
        domain={[min, max]}
        features={features}
        colors="blues"
        unknownColor={colors["color-grey-light"]}
        label="properties.name"
        valueFormat=".2s"
        projectionType="equirectangular"
        projectionTranslation={[0.5, 0.5]}
        projectionRotation={[0, 0, 0]}
        borderWidth={0.5}
        borderColor={colors["color-grey-light"]}
        tooltip={
          showTooltip
            ? (tooltipProps: { feature: ChoroplethBoundFeature }) => (
                <ChartTooltip.Geo {...tooltipProps.feature} showAbsolute={showAbsoluteTooltip} />
              )
            : undefined
        }
        isInteractive={showTooltip}
        legends={
          showLegend
            ? [
                {
                  anchor: "bottom-left",
                  direction: "column",
                  justify: true,
                  translateY: -75,
                  itemsSpacing: 0,
                  itemWidth: 94,
                  itemHeight: 18,
                  itemDirection: "left-to-right",
                  itemTextColor: colors["color-ui-interface"],
                  itemOpacity: 0.85,
                  symbolSize: 18,
                  effects: [
                    {
                      on: "hover",
                      style: {
                        itemTextColor: "#000000",
                        itemOpacity: 1,
                      },
                    },
                  ],
                },
              ]
            : undefined
        }
        {...otherProps}
      />
    </div>
  );
});
