import classNames from "classnames";
import React from "react";
import { Button, ButtonProps } from "../Button";
import { Checkbox } from "../CheckBox";
import { textLinkStyles } from "../TextLink";
import styles from "./ModalBody.module.css";

interface ActionLinkType {
  label: string;
  variant: "tertiary";
  color?: string;
  onClick: () => void;
}

export type ActionButton = ActionLinkType | Omit<ButtonProps, "ref">;

export interface ActionButtonsCta {
  type: "buttons";
  buttons: ActionButton[];
  centerAlign?: boolean;
}

export interface CheckboxButtonCta {
  type: "checkbox-button";
  checkboxLabel: string;
  buttonLabel: string;
  onButtonClick: () => void;
  isChecked: boolean;
  setIsChecked: (v: boolean) => void;
  buttonDisabled?: boolean;
}

export type CallToAction = ActionButtonsCta | CheckboxButtonCta;

interface CtaSectionProps {
  callToAction: CallToAction;
}

export const CtaSection: React.FC<CtaSectionProps> = (props: CtaSectionProps) => {
  const { callToAction } = props;

  if (callToAction.type === "buttons") {
    const ctas = callToAction.buttons;

    const primaries = ctas.filter((c) => c.variant === "primary") as Omit<ButtonProps, "ref">[];
    const secondaries = ctas.filter((c) => c.variant === "secondary") as Omit<ButtonProps, "ref">[];
    const tertiaries = ctas.filter((c) => c.variant === "tertiary") as ActionLinkType[];

    return (
      <div className={classNames(styles.buttonsCtaSection, { [styles.centerAlign]: callToAction.centerAlign })}>
        {tertiaries.map(({ onClick, label, color }, i) => (
          <div key={i} className={textLinkStyles} onClick={onClick} style={{ color: color }}>
            {label}
          </div>
        ))}
        {secondaries.map(({ onClick, label, ...extraProps }, i) => (
          <Button variant="secondary" key={i} label={label} onClick={onClick} {...extraProps}></Button>
        ))}
        {primaries.map(({ onClick, label, ...extraProps }, i) => (
          <Button variant="primary" key={i} label={label} onClick={onClick} {...extraProps}></Button>
        ))}
      </div>
    );
  } else if (callToAction.type === "checkbox-button") {
    return (
      <div className={classNames(styles.checkboxButtonCtaSection, styles.marginToPadding)}>
        <Checkbox
          id="modal-cta"
          label={callToAction.checkboxLabel}
          checked={callToAction.isChecked}
          onChange={(e) => callToAction.setIsChecked(e.target.checked)}
        />
        <Button
          label={callToAction.buttonLabel}
          disabled={callToAction.buttonDisabled}
          onClick={callToAction.onButtonClick}
        />
      </div>
    );
  }
  return null;
};
