import * as React from "react";
import classNames from "classnames";

import styles from "./Pill.module.css";
import skeletonStyles from "../../theme/skeleton.module.css";
import { Typography } from "../Typography";
import { InfoIcon, RightArrowIcon, CircleAlertIcon } from "../../icons";
import { IconButton } from "../IconButton";

type PillVariant = "default" | "small";

export interface PillProps {
  /**
   * Variant for pill sizing
   */
  variant?: PillVariant;
  /**
   * The label at the top of the pill
   */
  label: string;
  /**
   * Content to display in the body of pill
   */
  content: string;
  /**
   * Passing in a click handler will render an arrow button
   */
  onClick?: () => void;
  /**
   * Passing in a label icon click handler will render an info button
   */
  onClickInfo?: () => void;
  /**
   * Passing in a label icon click handler will render an alert button
   */
  onClickAlert?: () => void;
  /**
   * Specify an optional className to be applied to the container node
   */
  className?: string;
  /**
   * Set to skeleton mode
   */
  isSkeleton?: boolean;
  /**
   * ref to pass to the indicator component
   */
  indicatorRef?: React.Ref<HTMLButtonElement>;
}

export const Pill = React.forwardRef<HTMLDivElement, PillProps>(function Pill(props, ref) {
  const {
    variant = "default",
    label,
    content,
    onClick,
    onClickInfo,
    className,
    isSkeleton,
    onClickAlert,
    indicatorRef,
    ...otherProps
  } = props;
  return (
    <div
      ref={ref}
      aria-busy={isSkeleton}
      className={classNames(
        styles.container,
        {
          [styles.large]: variant === "default" && !!onClick,
          [styles.small]: variant === "small",
          [skeletonStyles.skeleton]: isSkeleton,
          [styles.skeleton]: isSkeleton,
        },
        className
      )}
      {...otherProps}
    >
      <header className={styles.header}>
        <Typography className={styles.label} variant="small">
          {label}
        </Typography>
        {onClickInfo && (
          <IconButton label={label} onClick={onClickInfo} ref={indicatorRef} data-testid="pill-info-button">
            <InfoIcon fillColor="var(--color-slate)" className={styles.rightIcon} />
          </IconButton>
        )}
        {onClickAlert && (
          <IconButton label={label} onClick={onClickAlert} ref={indicatorRef} data-testid="pill-alert-button">
            <CircleAlertIcon fillColor="var(--color-ui-negative)" className={styles.rightIcon} />
          </IconButton>
        )}
      </header>
      <Typography className={styles.content} variant={variant === "default" ? "h2" : "h3"}>
        {content}
      </Typography>
      {onClick && <RightArrowIcon className={styles.arrow} fillColor="var(--color-slate)" />}
    </div>
  );
});
