import * as React from "react";
import { PaginationContent } from "./PaginationContent";

export interface PaginationProps {
  /**
   * Total number of items to paginate
   */
  total: number;
  /**
   * Amount of items per page
   */
  perPage: number;
  /**
   * List of options for items per page
   */
  perPageOptions: number[];
  /**
   * Current page index - '1' indexed
   */
  pageIndex: number;
  /**
   * Callback on page index update
   */
  onUpdatePageIndex: (newPageIndex: number) => void;
  /**
   * Callback on per page update
   */
  onUpdatePerPage: (newPerPage: number) => void;
  /**
   * If component is in a loading state
   */
  isSkeleton?: boolean;
  /**
   * Accessibilty label
   */
  label?: string;
}

/**
 * Clamp page index
 *
 * @param index - The desired page index
 * @param perPage - Amount of items per page
 * @param total - Total amount of items
 * @returns A valid page index between 0 and maximum page index
 */
function clampPageIndex(index: number, perPage: number, total: number): number {
  const maxPage = Math.ceil(total / perPage);
  return Math.max(1, Math.min(maxPage, index));
}

export const Pagination: React.FC<PaginationProps> = ({
  total,
  perPage,
  perPageOptions,
  pageIndex,
  onUpdatePageIndex,
  onUpdatePerPage,
  isSkeleton,
  label,
}) => {
  const handlePrev = () => {
    onUpdatePageIndex(clampPageIndex(pageIndex - 1, perPage, total));
  };

  const handleNext = () => {
    onUpdatePageIndex(clampPageIndex(pageIndex + 1, perPage, total));
  };

  const handleSelectPerPage = (perPageValue: string) => {
    onUpdatePerPage(+perPageValue);
    onUpdatePageIndex(clampPageIndex(pageIndex, +perPageValue, total));
  };

  return (
    <PaginationContent
      onSelectPerPage={handleSelectPerPage}
      onPrev={handlePrev}
      onNext={handleNext}
      pageIndex={pageIndex}
      perPage={perPage}
      perPageOptions={perPageOptions.map((option) => option + "")}
      total={total}
      isSkeleton={isSkeleton}
      label={label}
    />
  );
};
