import * as React from "react";
import classNames from "classnames";

import styles from "./IndicatorLabel.module.css";
import skeletonStyles from "../../theme/skeleton.module.css";

export interface IndicatorLabelProps {
  className?: string;
  label: string;
  color?: string;
  /**
   * Specify dot colour to show dot in top right
   */
  dotColor?: string;
  isSkeleton?: boolean;
}

export const IndicatorLabel = React.forwardRef<HTMLDivElement, IndicatorLabelProps>(function IndicatorLabel(
  props,
  ref
) {
  const { className, label, color, isSkeleton, dotColor, ...otherProps } = props;
  return (
    <div
      ref={ref}
      aria-busy={isSkeleton}
      className={classNames(
        styles.container,
        { [skeletonStyles.skeleton]: isSkeleton, [styles.skeleton]: isSkeleton },
        className
      )}
      style={{ color: isSkeleton ? undefined : color }}
      {...otherProps}
    >
      <span className={styles.label}>{label}</span>
      {dotColor && (
        <span
          className={classNames(styles.dot, { [styles.skeleton]: isSkeleton })}
          style={{ backgroundColor: isSkeleton ? undefined : dotColor }}
        />
      )}
    </div>
  );
});
