import * as React from "react";
import classNames from "classnames";

import styles from "./YearPicker.module.css";
import { addYears, endOfMonth, setYear, startOfMonth, startOfYear, subYears } from "date-fns";
import { IconButton } from "../IconButton";
import { ChevronRightIcon } from "../../icons";
import { DateRange } from "@vkbansal/react-date-primitives";

export interface YearPickerProps {
  className?: string;
  selectedDate?: Date;
  boundingRange?: DateRange;
  numYears?: number;
  onYearSelect?: (date: Date) => void;
}

export const YearPicker = React.forwardRef<HTMLDivElement, YearPickerProps>(function YearPicker(
  { className, boundingRange, selectedDate = new Date(), onYearSelect, numYears = 10 },
  ref
) {
  const [currentVisibleDate, setCurrentVisibleDate] = React.useState(selectedDate);
  const [minDate, maxDate] = boundingRange || [];

  const visibleYears = React.useMemo(() => {
    const currentYear = currentVisibleDate.getFullYear();
    return Array.from(Array(numYears).keys()).map((n) => n - numYears + currentYear + 1);
  }, [currentVisibleDate, numYears]);

  const isInRange = React.useCallback(
    (date: Date) =>
      (minDate ? startOfYear(date) >= startOfYear(minDate) : true) &&
      (maxDate ? startOfYear(date) <= startOfYear(maxDate) : true),
    [maxDate, minDate]
  );

  const showPrevious = React.useMemo(
    () => isInRange(endOfMonth(subYears(currentVisibleDate, numYears))),
    [currentVisibleDate, isInRange, numYears]
  );

  const showNext = React.useMemo(
    () => isInRange(startOfMonth(addYears(currentVisibleDate, 1))),
    [currentVisibleDate, isInRange]
  );

  const onPreviousClick = React.useCallback(() => {
    const newYear = subYears(currentVisibleDate, numYears);
    setCurrentVisibleDate(newYear);
  }, [currentVisibleDate, numYears]);

  const onNextClick = React.useCallback(() => {
    const newYear = addYears(currentVisibleDate, numYears);
    setCurrentVisibleDate(newYear);
  }, [currentVisibleDate, numYears]);

  return (
    <div ref={ref} className={classNames(styles.wrapper, className)} data-testid="year-picker">
      <div className={styles.picker}>
        <div className={styles.header}>
          <div className={styles.pickerControl} data-testid="year-picker-header">
            {currentVisibleDate.getFullYear() - numYears + 1}
            {" - "}
            {currentVisibleDate.getFullYear()}
          </div>
          <div className={styles.controls}>
            {showPrevious && (
              <IconButton
                label="Previous Years"
                data-testid="year-picker-previous-button"
                className={styles.control}
                onClick={onPreviousClick}
              >
                <ChevronRightIcon className={styles.chevronLeft} />
              </IconButton>
            )}
            {showNext && (
              <IconButton
                label="Next Years"
                data-testid="year-picker-next-button"
                className={styles.control}
                onClick={onNextClick}
              >
                <ChevronRightIcon />
              </IconButton>
            )}
          </div>
        </div>
        <div className={styles.years}>
          {visibleYears.map((year) => (
            <div
              data-testid="year-picker-year"
              data-active-year={year === selectedDate.getFullYear()}
              data-inrange={isInRange(setYear(selectedDate, year))}
              className={styles.cell}
              key={year}
              onClick={(): void => {
                onYearSelect && onYearSelect(setYear(selectedDate, year));
              }}
            >
              {year}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
});
