import * as React from "react";
import classNames from "classnames";

import styles from "./IndicatorPill.module.css";
import skeletonStyles from "../../theme/skeleton.module.css";

export interface IndicatorPillProps {
  className?: string;
  label: string;

  leftIcon?: React.ReactNode;
  color?: string;
  isSkeleton?: boolean;
}

export const IndicatorPill = React.forwardRef<HTMLDivElement, IndicatorPillProps>(function IndicatorPill(props, ref) {
  const { className, label, color, isSkeleton, leftIcon, ...otherProps } = props;
  return (
    <div
      ref={ref}
      aria-busy={isSkeleton}
      className={classNames(
        styles.container,
        { [skeletonStyles.skeleton]: isSkeleton, [styles.skeleton]: isSkeleton },
        className
      )}
      style={{ color: isSkeleton ? undefined : color }}
      {...otherProps}
    >
      <div className={classNames(styles.label, { [styles.withLeftIcon]: leftIcon })}>
        {leftIcon}
        <span>{label}</span>
      </div>
    </div>
  );
});
