import * as React from "react";
import classNames from "classnames";

import styles from "./HighlightText.module.css";

type HighlightTextProps = React.HTMLAttributes<HTMLSpanElement> & { search?: string };

export const HighlightText = React.forwardRef<HTMLSpanElement, HighlightTextProps>(function HighlightText(props, ref) {
  const { className, search, children, ...otherProps } = props;
  const highlighted = React.Children.map(children, (child) => {
    if (typeof child === "string" && search) {
      try {
        // Create regex from search text
        const searchRe = new RegExp(search, "ig");
        let res = null;
        let lastIndex = 0;
        const highlightChild = [];
        // Loop over matches and build jsx
        while ((res = searchRe.exec(child)) !== null) {
          // Get first portion
          highlightChild.push(child.slice(lastIndex, res.index));
          // Wrap matched portion in <mark />
          highlightChild.push(<mark>{child.slice(res.index, res.index + res[0].length)}</mark>);
          // Update last index for next iteration
          lastIndex = searchRe.lastIndex;
        }
        // Remaining text
        highlightChild.push(child.slice(lastIndex));
        return highlightChild;
      } catch (err) {
        // If we can't create a regex from the search text, render the child as normal, without highlighting
        return child;
      }
    }
    return child;
  });
  return (
    <span ref={ref} className={classNames(styles.container, className)} {...otherProps}>
      {highlighted}
    </span>
  );
});
