import * as React from "react";
import classNames from "classnames";
import styles from "./Grid.module.css";

export interface RowProps {
  className?: string;
  /**
   * Specify to collapse vertical margin at base
   */
  collapseMargin?: boolean;
}

export const Row: React.FC<RowProps> = ({ className, collapseMargin, children, ...otherProps }) => {
  return (
    <div className={classNames(styles.row, { [styles.collapse]: collapseMargin }, className)} {...otherProps}>
      {children}
    </div>
  );
};
