import * as React from "react";
import classNames from "classnames";

import styles from "./Grid.module.css";

export interface GridProps {
  /**
   * Specify an optional className for container node
   */
  className?: string;
  /**
   * Show columns for debugging
   */
  isDebug?: boolean;
  /**
   * Render the grid with no max-width
   */
  fullWidth?: boolean;
}

export const Grid: React.FC<GridProps> = ({ className, isDebug, fullWidth, children, ...otherProps }) => {
  return (
    <div
      className={classNames(styles.container, className, { [styles.debug]: isDebug, [styles.fullWidth]: fullWidth })}
      {...otherProps}
    >
      {children}
    </div>
  );
};
