import React from "react";
import classNames from "classnames";

import styles from "./IconLabel.module.css";
import { Typography } from "../Typography";

export interface IconLabelProps {
  /**
   * Specify the label
   */
  label?: string;
  /**
   * Optional description text
   */
  description?: string;
  /**
   * Specify an optional className applied to the container node
   */
  className?: string;
  /**
   * Specify an optional className applied to the container node
   */
  labelClassName?: string;
  /**
   * Specify an optional className applied to the container node
   */
  descriptionClassName?: string;
  /**
   * Specify an optional click handler for interactivity
   */
  onClick?: (e: React.MouseEvent<HTMLDivElement>) => void;
  /**
   * Skeleton mode
   */
  isSkeleton?: boolean;
  /**
   * Skeleton width of label
   */
  labelSkeletonWidth?: string;
  /**
   * Skeleton width of description
   */
  descSkeletonWidth?: string;
  children?: React.ReactNode;
}

export const IconLabel = React.forwardRef<HTMLDivElement, IconLabelProps>(function IconLabel(props, ref) {
  const {
    children,
    label,
    description,
    className,
    labelClassName,
    descriptionClassName,
    onClick,
    isSkeleton,
    labelSkeletonWidth = "2rem",
    descSkeletonWidth = "4rem",
    ...otherProps
  } = props;
  return (
    <div
      ref={ref}
      aria-busy={isSkeleton}
      onClick={onClick}
      className={classNames(
        styles.container,
        { [styles.clickable]: onClick, [styles.skeleton]: isSkeleton },
        className
      )}
      {...otherProps}
    >
      <div className={classNames(styles.icon, { [styles.skeletonIcon]: isSkeleton })}>{children}</div>
      <div className={styles.labelContainer}>
        <Typography
          isSkeleton={isSkeleton}
          skeletonWidth={labelSkeletonWidth}
          skeletonHeight="1rem"
          className={classNames(styles.label, labelClassName)}
          variant="p"
        >
          {label}
        </Typography>
        <Typography
          isSkeleton={isSkeleton}
          skeletonWidth={descSkeletonWidth}
          skeletonHeight="0.875rem"
          className={classNames(styles.description, descriptionClassName)}
          variant="extraSmall"
        >
          {description}
        </Typography>
      </div>
    </div>
  );
});
