import * as React from "react";
import classNames from "classnames";

import styles from "./Switch.module.css";
import skeletonStyles from "../../theme/skeleton.module.css";

export interface SwitchProps {
  /**
   * Control the input on/off state
   */
  isChecked: boolean;
  /**
   * Specify a label to associate with switch button
   */
  label?: string;
  /**
   * Click handler for button
   */
  onClick?: (e: React.MouseEvent<HTMLButtonElement>) => void;
  /**
   * Readonly state
   */
  readonly?: boolean;
  /**
   * Skeleton mode
   */
  isSkeleton?: boolean;
  className?: string;
  labelClassName?: string;
  buttonClassName?: string;
}

type Props = Omit<React.ComponentPropsWithRef<"div">, keyof SwitchProps> & SwitchProps;

export const Switch = React.forwardRef<HTMLDivElement, Props>(function Switch(props, ref) {
  const {
    id,
    onClick,
    readonly,
    isChecked,
    buttonClassName,
    labelClassName,
    className,
    label,
    isSkeleton,
    ...otherProps
  } = props;

  return (
    <div aria-busy={isSkeleton} ref={ref} className={classNames(styles.container, className)} {...otherProps}>
      <button
        id={id}
        onClick={onClick}
        className={classNames(styles.button, buttonClassName, {
          [skeletonStyles.skeleton]: isSkeleton,
          [styles.skeleton]: isSkeleton,
        })}
        type="button"
        role="switch"
        disabled={isSkeleton}
        aria-checked={isChecked ? "true" : "false"}
        aria-readonly={readonly}
      >
        <span>{isChecked ? "On" : "Off"}</span>
      </button>
      {label && (
        <label
          className={classNames(styles.label, labelClassName, { [skeletonStyles.skeleton]: isSkeleton, [styles.labelSkeleton]: isSkeleton })}
          htmlFor={id}
        >
          {label}
        </label>
      )}
    </div>
  );
});
