import classNames from "classnames";
import React from "react";

import styles from "./Checkbox.module.css";
import skeletonStyles from "../../theme/skeleton.module.css";
import { CheckIcon } from "../../icons";

export interface CheckboxProps {
  /**
   * ID for the component. This must be unique for the checkbox to work.
   */
  id: string;
  /**
   * Display label for the checkbox
   */
  label?: string;
  isSkeleton?: boolean;
  labelClassName?: string;
  checkClassName?: string;
  wrapperClassName?: string;
}

type Props = Omit<React.ComponentPropsWithRef<"input">, keyof CheckboxProps> & CheckboxProps;

export const Checkbox = React.forwardRef<HTMLInputElement, Props>(function Checkbox(props, ref) {
  const {
    id,
    onChange,
    checked,
    labelClassName,
    checkClassName,
    wrapperClassName,
    label,
    isSkeleton,
    ...otherProps
  } = props;

  const readonly = !onChange;

  return (
    <div className={classNames(styles.wrapper, wrapperClassName)}>
      <input
        className={styles.input}
        aria-busy={isSkeleton}
        ref={ref}
        checked={checked}
        type="checkbox"
        id={id}
        onChange={onChange}
        readOnly={readonly}
        {...otherProps}
      />
      <label
        htmlFor={id}
        className={classNames(styles.label, labelClassName, {
          [styles.readonly]: readonly,
          [styles.labelSkeleton]: isSkeleton,
        })}
      >
        <CheckIcon
          className={classNames(styles.checkIcon, checkClassName, {
            [skeletonStyles.skeleton]: isSkeleton,
            [styles.checkSkeleton]: isSkeleton,
          })}
        />
        {label && (
          <span
            className={classNames({
              [skeletonStyles.skeleton]: isSkeleton,
              [styles.labelTextSkeleton]: isSkeleton,
            })}
          >
            {label}
          </span>
        )}
      </label>
    </div>
  );
});
