import * as React from "react";
import classNames from "classnames";

import styles from "./ErrorDialog.module.css";
import { Typography } from "../Typography";
import { Card } from "../Card";

export const ERROR_404 = "Page doesn't exist";
export const ERROR_GENERIC = "Something went wrong";

function getErrorTitle(statusCode: number): string {
  if (statusCode === 404) {
    return ERROR_404;
  }
  return ERROR_GENERIC;
}

export interface ErrorDialogProps {
  /**
   * Status code of error
   */
  statusCode: number;
  /**
   * Correlation id of error
   */
  correlationId?: string;
  /**
   * Additional information message
   */
  message?: string;
  /**
   * Specify a custom class name
   */
  className?: string;
  /**
   * Specify whether to wrap in Card component
   */
  isCard?: boolean;
}

export const ErrorDialog: React.FC<ErrorDialogProps> = ({
  statusCode,
  correlationId,
  message,
  className,
  isCard,
  ...otherProps
}) => {
  const content = (
    <>
      <header className={styles.header}>
        <Typography id="error-dialog-title" variant="extraLarge">
          {statusCode}
        </Typography>
      </header>
      <div className={styles.body}>
        <Typography id="error-dialog-desc" variant="h2">
          {getErrorTitle(statusCode)}
        </Typography>
        {correlationId && <Typography variant="small">{correlationId}</Typography>}
        {message && <Typography>{message}</Typography>}
      </div>
    </>
  );

  return (
    <div
      role="dialog"
      aria-labelledby="error-dialog-title"
      aria-describedby="error-dialog-desc"
      className={classNames(styles.container, className)}
      {...otherProps}
    >
      {isCard ? <Card>{content}</Card> : content}
    </div>
  );
};
