import * as React from "react";

import styles from "./EntityInfo.module.css";
import { Typography } from "../Typography";

export interface EntityInfoProps { 
  /**
  * title will be wrapped in a Typography component - avoid child divs
  */
  title: React.ReactNode;
  /**
  * subtitle will be wrapped in a Typography component - avoid child divs
  */
  subtitle: React.ReactNode;
  className?: string;
}

export const EntityInfo = React.forwardRef<HTMLDivElement, EntityInfoProps>(function EntityInfo(props, ref) {
  const { className, title, subtitle, ...otherProps } = props;
  return (
    <div ref={ref} className={className} {...otherProps}>
      <Typography className={styles.title} variant="p">
        {title}
      </Typography>
      <Typography className={styles.subtitle} variant="extraSmall">
        {subtitle}
      </Typography>
    </div>
  );
});
