import * as React from "react";
import { usePopper } from "react-popper";
import { Modifier, ModifierPhases, Options, Window } from "@popperjs/core";
import { animated, useSpring } from "react-spring";
import usePortal from "react-useportal";
import camelCase from "camelcase";
import classNames from "classnames";

import styles from "./Popover.module.css";

export type PopoverSize = "fixed" | "large" | "full" | "auto";

export type PopoverProps = {
  onClose: () => void;
  isOpen: boolean;
  /**
   * @deprecated use size instead
   */
  fullWidth?: boolean;
  size?: PopoverSize;
  disableOverlay?: boolean;
  /**
   * Disable scale up transition
   */
  disableScale?: boolean;
  anchorElement?: HTMLElement;
  popperOptions?: Partial<Options>;
  className?: string;
  popoverClassName?: string;
};

export const Popover = React.forwardRef<HTMLElement, React.PropsWithChildren<PopoverProps>>(function Popover(
  props,
  ref
) {
  const {
    onClose,
    popperOptions,
    anchorElement,
    isOpen,
    className,
    popoverClassName,
    fullWidth,
    size = "fixed",
    disableOverlay,
    disableScale,
    children,
    ...otherProps
  } = props;
  const { Portal } = usePortal();
  const tooltipRef = React.useRef<HTMLDivElement | null>(null);
  const [popoverRef, setPopoverRef] = React.useState<HTMLDivElement | null>(null);

  const springValues: { opacity: number; transform?: string } = {
    opacity: isOpen ? 1 : 0,
  };

  if (!disableScale) {
    springValues.transform = isOpen ? "scale(1)" : "scale(0.95)";
  }

  const springProps = useSpring(springValues);

  const modifiers = React.useMemo(
    () =>
      [
        ...(popperOptions?.modifiers ?? [
          {
            name: "offset",
            options: {
              offset: [8, 8],
            },
          },
        ]),
        fullWidth || size === "full"
          ? ({
              name: "sameWidth",
              enabled: true,
              phase: "beforeWrite" as ModifierPhases,
              requires: ["computeStyles"],
              fn({ state }) {
                state.styles.popper.minWidth = `${state.rects.reference.width}px`;
              },
              effect({ state }) {
                state.elements.popper.style.minWidth = `${(state.elements.reference as HTMLElement).offsetWidth}px`;
              },
            } as Modifier<"sameWidth", Window>)
          : {},
      ].filter(Boolean),
    [size, fullWidth, popperOptions?.modifiers]
  );

  const options = {
    ...popperOptions,
    modifiers,
  };

  const { styles: popperStyles, attributes: popperAttrs } = usePopper(anchorElement, popoverRef, options);
  const handleKeyDown = React.useCallback(
    (event) => {
      if (event.key === "Escape") {
        onClose();
      }
    },
    [onClose]
  );

  const handleClickOverlay = (event: React.MouseEvent<HTMLDivElement>) => {
    event.preventDefault();
    event.stopPropagation();
    onClose();
  };

  const handleMouseUp = React.useCallback(
    (event: MouseEvent) => {
      const { current } = tooltipRef;
      if (event.target instanceof Element && current && current.contains(event.target)) {
        event.stopPropagation();
        return;
      }
      onClose();
    },
    [onClose]
  );

  React.useEffect(() => {
    if (isOpen) {
      window.addEventListener("keydown", handleKeyDown, true);
      if (disableOverlay) {
        window.addEventListener("mouseup", handleMouseUp, true);
      }
      return () => {
        window.removeEventListener("keydown", handleKeyDown, true);
        if (disableOverlay) {
          window.removeEventListener("mouseup", handleMouseUp, true);
        }
      };
    }
  }, [isOpen, disableOverlay, handleKeyDown, handleMouseUp]);

  const placementClassName = camelCase(popperOptions?.placement ? popperOptions.placement : "");

  const resolvedSize = fullWidth ? "full" : size;

  return (
    <Portal ref={ref}>
      {isOpen && !disableOverlay && <div role="dialog" onClick={handleClickOverlay} className={styles.overlay} />}
      <div
        data-testid="popover"
        className={classNames(styles.popper, { [styles.open]: isOpen }, styles[resolvedSize], className)}
        ref={setPopoverRef}
        style={popperStyles.popper}
        {...popperAttrs.popper}
      >
        <animated.div
          ref={tooltipRef}
          role="tooltip"
          aria-label="popover"
          aria-hidden={isOpen ? undefined : "true"}
          className={classNames(styles.popover, styles[placementClassName], popoverClassName)}
          style={springProps}
          {...otherProps}
        >
          {children}
        </animated.div>
      </div>
    </Portal>
  );
});
