import * as React from "react";
import classNames from "classnames";

import styles from "./Tabs.module.css";

export interface TabsProps {
  className?: string;
  /**
   * Specify a label to identify tab list
   */
  label: string;
  /**
   * Id of selected tab
   */
  selectedTabId: string;
  /**
   * Handler for changing tab value
   * @param e React mouse event
   * @param value Value of new tab id
   */
  onTabChange(e: React.MouseEvent<HTMLButtonElement>, value: string): void;
}

export const Tabs: React.FC<TabsProps> = ({
  label,
  selectedTabId,
  onTabChange,
  className,
  children,
  ...otherProps
}) => {
  const tabChildren = React.Children.map(children, (child) => {
    if (!React.isValidElement(child)) {
      return null;
    }

    const childTabId = child.props.id;
    return React.cloneElement(child, {
      onTabChange,
      isSelected: childTabId === selectedTabId,
    });
  });

  return (
    <div className={classNames(styles.container, className)} {...otherProps}>
      <div role="tablist" aria-label={label} className={styles.tablist}>
        {tabChildren}
      </div>
    </div>
  );
};
