import * as React from "react";
import classNames from "classnames";
import styles from "./DataTable.module.css";
import skeletonStyles from "../../theme/skeleton.module.css";
import TableHeader from "./TableHeader";
import { ColumnDefinition, DataDefinition, RowInfo, SortDirection } from "./types";
import TableBody from "./TableBody";
import { DEFAULT_HEADER_HEIGHT, DEFAULT_NUM_SKELETON_ROWS, DEFAULT_ROW_HEIGHT } from "./constants";

export interface DataTableProps<T extends DataDefinition[], K> {
  isSkeleton?: boolean;
  skeletonRows?: number;
  skeletonRowHeight?: number;
  skeletonHeaderHeight?: number;
  className?: string;
  /**
   * Automatically size columsn
   */
  isLayoutAuto?: boolean;
  /**
   * Rows of data to be presented in table
   */
  rowsInfo: RowInfo<T>[];
  /**
   * Sort key to identify sorted column
   */
  sortKey?: K;
  /**
   * Specify sort direction to show indicator arrow
   */
  sortDirection?: SortDirection;
  /**
   * Sort handler for click on column header
   */
  onSort?: (key: K) => void;
  /**
   * Column definitions (accepts generic key type)
   */
  columnDefs: ColumnDefinition<K>[];
}

export function DataTable<T extends DataDefinition[], K>(props: DataTableProps<T, K>): JSX.Element {
  const {
    className,
    rowsInfo,
    columnDefs,
    sortKey,
    sortDirection,
    onSort,
    isLayoutAuto,
    isSkeleton,
    skeletonRows = DEFAULT_NUM_SKELETON_ROWS,
    skeletonRowHeight = DEFAULT_ROW_HEIGHT,
    skeletonHeaderHeight = DEFAULT_HEADER_HEIGHT,
    ...otherProps
  } = props;

  const skeletonStyle = {
    height: skeletonRows * skeletonRowHeight + skeletonHeaderHeight
  };

  return (
    <div
      aria-busy={isSkeleton}
      className={classNames(styles.container, className, {
        [skeletonStyles.skeleton]: isSkeleton,
        [styles.skeleton]: isSkeleton,
        [styles.layoutAuto]: isLayoutAuto
      })}
      {...otherProps}
      style={isSkeleton ? skeletonStyle : undefined}
    >
      <table className={styles.table}>
        <thead>
          <tr className={styles.row}>
            {columnDefs.map((def, i) => (
              <TableHeader
                key={i}
                className={def.className}
                colWidth={def.colWidth}
                isSort={!!def.sortable}
                currentSortKey={sortKey}
                sortKey={def.key}
                sortDirection={sortDirection}
                label={def.label}
                onClick={(e) => {
                  e.preventDefault();
                  if (onSort && def.sortable) {
                    onSort(def.key);
                  }
                }}
              />
            ))}
          </tr>
        </thead>
        <TableBody rowsInfo={rowsInfo} columnDefs={columnDefs} />
      </table>
    </div>
  );
}
