import React, { useContext, useEffect } from "react";
import { ToastProvider } from "../Toast";

const MODAL_ID = "orc-portal-modal";
const TOAST_ID = "orc-portal-toast";

interface IOrchardContext {
  modalPortalId: string;
  toastPortalId: string;
}

const OrchardContext = React.createContext<IOrchardContext | null>(null);

export function OrchardProvider({ children }: { children?: React.ReactNode }): JSX.Element {
  const { Provider } = OrchardContext;

  useEffect(() => {
    let modalEl = document.getElementById(MODAL_ID);
    if (!modalEl) {
      modalEl = document.createElement("div");
      modalEl.id = MODAL_ID;
      document.body.appendChild(modalEl);
    }

    let toastEl = document.getElementById(TOAST_ID);
    if (!toastEl) {
      toastEl = document.createElement("div");
      toastEl.id = TOAST_ID;
      document.body.appendChild(toastEl);
    }

    return () => {
      const modalElem = document.getElementById(MODAL_ID);
      if (modalElem) modalElem.remove();

      const toastElem = document.getElementById(TOAST_ID);
      if (toastElem) toastElem.remove();
    };
  });

  return (
    <Provider value={{ modalPortalId: MODAL_ID, toastPortalId: TOAST_ID }}>
      <ToastProvider portalId={TOAST_ID}>{children}</ToastProvider>
    </Provider>
  );
}

export const useOrchard = (): { modalPortalId: string; toastPortalId: string } => {
  const context = useContext(OrchardContext);

  if (!context) {
    throw new Error("You must initialise an `OrchardProvider` before using Orchard modals or toasts");
  }

  return context;
};
