import * as React from "react";
import classNames from "classnames";

import { PointTooltipProps, Serie } from "@nivo/line";
import styles from "./ChartTooltip.module.css";
import { Typography } from "../Typography";
import { ChoroplethBoundFeature } from "@nivo/geo";
import { FlagIcon } from "../..";
import { BarDatum, BarTooltipProps } from "@nivo/bar";
import { BarChartData } from "../BarChart/types";
import { PieDataShape } from "../PieChart/types";
import { PieTooltipProps } from "@nivo/pie";

export interface LineChartTooltipProps extends PointTooltipProps {
  data: Serie[];
  className?: string;
  formatTooltipItem?: (value: string | number | Date | null | undefined) => string;
}

const LineChartTooltip = React.forwardRef<HTMLDivElement, LineChartTooltipProps>(function LineChartTooltip(props, ref) {
  const { data, formatTooltipItem = (value) => value, className, ...tooltipProps } = props;
  const { point } = tooltipProps;
  const tooltipItems = data.map((d) => ({
    series: d.id,
    item: d.data.find((item) => item.x === point.data.xFormatted),
    color: d.color,
  }));
  return (
    <div ref={ref} className={classNames(styles.container, className)}>
      {tooltipItems.map((dataItem, index) => (
        <div key={index} className={styles.item}>
          <div style={{ color: dataItem.color }} className={styles.line} />
          <Typography className={styles.label} variant="small">
            {formatTooltipItem(dataItem.item?.y)}
          </Typography>
        </div>
      ))}
    </div>
  );
});

export interface GeoChartTooltipProps extends ChoroplethBoundFeature {
  data: {
    id: string;
    value: number;
  }[];
  showAbsolute?: boolean;
  className?: string;

  // These fields are not present in the types but come through as props
  id?: string;
  properties?: { name: string };
}

const GeoChartTooltip = React.forwardRef<HTMLDivElement, GeoChartTooltipProps>(function GeoChartTooltip(props, ref) {
  const {
    id,
    label,
    properties,
    value,
    formattedValue,
    showAbsolute = true,
    className,
  } = props as GeoChartTooltipProps;

  return (
    <div ref={ref} className={classNames(styles.container, className)}>
      <div className={styles.item}>
        {id && <FlagIcon country={id} />}
        <div className={styles.stackedLabel}>
          <Typography className={styles.label} variant="small">
            <strong>{label || properties?.name}</strong>
          </Typography>
          <Typography className={styles.label} variant="small">
            {showAbsolute && value ? value.toFixed(2) : formattedValue || "-"}
          </Typography>
        </div>
      </div>
    </div>
  );
});

export interface BarChartTooltipProps extends Omit<BarTooltipProps<BarDatum>, "data"> {
  className?: string;
  data: BarChartData;
}

const BarChartTooltip = React.forwardRef<HTMLDivElement, BarChartTooltipProps>(function BarChartTooltip(props, ref) {
  const { data, className, id, indexValue, value, color } = props;

  return (
    <div ref={ref} className={classNames(styles.container, className)}>
      <div className={styles.item}>
        <div style={{ color: color }} className={styles.box} />
        <Typography className={styles.label} variant="small">
          <strong>
            {data.data.find((k) => k.categoryKey === indexValue)?.name} - {data.keys.find((k) => k.dataKey === id)?.name}:
          </strong>
        </Typography>
        <Typography className={styles.label} variant="small">
          {value}
        </Typography>
      </div>
    </div>
  );
});

export interface PieChartTooltipProps extends PieTooltipProps<PieDataShape> {
  className?: string;
}

const PieChartTooltip = React.forwardRef<HTMLDivElement, PieChartTooltipProps>(function PieChartTooltip(props, ref) {
  const { className, datum } = props;

  return (
    <div ref={ref} className={classNames(styles.container, className)}>
      <div className={styles.item}>
        <div style={{ color: datum.color }} className={styles.box} />
        <Typography className={styles.label} variant="small">
          <strong>
            {datum.label}:
          </strong>
        </Typography>
        <Typography className={styles.label} variant="small">
          {datum.value}
        </Typography>
      </div>
    </div>
  );
});

export default {
  Line: LineChartTooltip,
  Geo: GeoChartTooltip,
  Bar: BarChartTooltip,
  Pie: PieChartTooltip,
};
