import * as React from "react";
import classNames from "classnames";
import styles from "./Typography.module.css";
import skeletonStyles from "../../theme/skeleton.module.css";

type TypographyVariantElement = "h1" | "h2" | "h3" | "h4" | "p";
type TypographyVariantOther = "small" | "extraSmall" | "large" | "extraLarge";
type TypographyVariant = TypographyVariantElement | TypographyVariantOther;

const variantTagMap: Record<TypographyVariant, TypographyVariantElement> = {
  h1: "h1",
  h2: "h2",
  h3: "h3",
  h4: "h4",
  p: "p",
  small: "p",
  extraSmall: "p",
  large: "p",
  extraLarge: "p",
};

/**
 * Typography custom props
 */
export type TypographyProps = {
  /**
   * Specify to render as skeleton
   */
  isSkeleton?: boolean;
  /**
   * Specify a skeleton width
   */
  skeletonWidth?: string | number;
  /**
   * Specify a skeleton height
   */
  skeletonHeight?: string | number;
  /**
   * Variant is the element type of typography
   */
  variant?: TypographyVariant;
};

export function Typography(props: React.ComponentPropsWithRef<TypographyVariantElement> & TypographyProps): JSX.Element {
  const {
    className,
    children,
    isSkeleton,
    skeletonWidth = "4rem",
    skeletonHeight = "1rem",
    variant = "p",
    ...otherProps
  } = props;

  /**
   * Variant mapped to valid ElementType
   * Capitalize to allow element to be created
   */
  const MappedElement = variantTagMap[variant];

  const skeletonStyle = isSkeleton
    ? {
        width: skeletonWidth,
        height: skeletonHeight,
      }
    : undefined;

  return (
    <MappedElement
      aria-busy={isSkeleton}
      className={classNames(
        styles[variant],
        { [skeletonStyles.skeleton]: isSkeleton, [styles.skeleton]: isSkeleton },
        className
      )}
      style={skeletonStyle}
      {...otherProps}
    >
      {isSkeleton ? null : children}
    </MappedElement>
  );
}
