import * as React from "react";
import classNames from "classnames";
import { ReloadIcon } from "../../icons/ReloadIcon";
import styles from "./Button.module.css";
import skeletonStyles from "../../theme/skeleton.module.css";

type ButtonVariant = "primary" | "secondary";
type SpinnerAlign = "centre" | "left" | "right";

type ButtonSize = "large" | "medium" | "small" | "extra-small";

type ButtonElementProps = JSX.IntrinsicElements["button"];
type AnchorElementProps = JSX.IntrinsicElements["a"];

type ButtonAnchorElementProps = Omit<ButtonElementProps & AnchorElementProps, "size">;

function isPropsForAnchor(props: AnchorElementProps | ButtonElementProps): props is AnchorElementProps {
  return "href" in props;
}

export interface ButtonProps extends ButtonAnchorElementProps {
  className?: string;
  label: string;
  isLoading?: boolean;
  variant?: ButtonVariant;
  size?: ButtonSize;
  color?: string;
  spinnerAlign?: SpinnerAlign;
  isSkeleton?: boolean;
  "data-testid"?: string;
}

export const Button = React.forwardRef<HTMLButtonElement | HTMLAnchorElement, ButtonProps>(function Button(props, ref) {
  const {
    className,
    isLoading,
    variant = "primary",
    spinnerAlign = "centre",
    size = "medium",
    label,
    color,
    isSkeleton,
    disabled,
    href,
    ...buttonProps
  } = props;
  const isDisabled = disabled || isSkeleton;

  const buttonClassNames = classNames(
    styles.button,
    styles[`spinner-${spinnerAlign}`],
    styles[`size-${size}`],
    {
      [styles.loading]: isLoading,
      [styles.secondary]: variant === "secondary",
      [skeletonStyles.skeleton]: isSkeleton,
      [styles.skeleton]: isSkeleton
    },
    className
  );

  const buttonContent = (
    <>
      <span aria-hidden={isSkeleton ? "true" : undefined} className={styles.label}>
        {label}
      </span>
      <span
        aria-hidden={isSkeleton ? "true" : undefined}
        className={styles.icon}
        data-testid={buttonProps["data-testid"] ? `${buttonProps["data-testid"]}-icon` : "button-icon"}
      >
        <ReloadIcon fillColor={variant === "secondary" ? (color ? color : "#1fcfec") : "#fff"} />
      </span>
    </>
  );

  return isPropsForAnchor(props) ? (
    <a
      ref={ref as React.ForwardedRef<HTMLAnchorElement>}
      style={{ color }}
      className={buttonClassNames}
      aria-busy={isSkeleton}
      aria-label={label}
      href={href}
      {...buttonProps}
    >
      {buttonContent}
    </a>
  ) : (
    <button
      ref={ref as React.ForwardedRef<HTMLButtonElement>}
      style={{ color }}
      className={buttonClassNames}
      aria-busy={isSkeleton}
      aria-label={label}
      disabled={isDisabled}
      {...buttonProps}
    >
      {buttonContent}
    </button>
  );
});

export const buttonStyles = {
  ...styles,
};
