import { addMonths } from "date-fns";

export const MONTH_NAMES = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];

export const SHORT_MONTH_NAMES = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];

export interface CalendarMonth {
  index: number;
  name: string;
  shortName: string;
}

export const ALL_MONTHS = MONTH_NAMES.map<CalendarMonth>((n, i) => ({
  index: i,
  name: n,
  shortName: SHORT_MONTH_NAMES[i],
}));

export const createMonthsRange = (startDate: Date, months: number): Date[] => {
  return Array.from(Array(months).keys()).map((d) => addMonths(startDate, d));
};
