import * as React from "react";
import { reducer, defaultState, ToastItem, ActionTypes, ToastInfo } from "./Toast.reducer";
import { Toast } from "./Toast";
import { ToastContainer } from "./ToastContainer";
import { useTransition } from "react-spring";

interface IToastContext {
  addToast: (toastInfo: ToastInfo) => void;
  removeToast: (id: string) => void;
  registerHeight: (id: string, height: number) => void;
}

const ToastContext = React.createContext<IToastContext | null>(null);

interface ToastProviderProps {
  children?: React.ReactNode;
  portalId?: string;
}

let toastCount = 0;
function createToast(info: ToastInfo): ToastItem {
  return {
    id: `toast-${toastCount++}`,
    combinationToasts: [],
    ...info,
  };
}

export function ToastProvider({ children, portalId }: ToastProviderProps): JSX.Element {
  const { Provider } = ToastContext;
  const [state, dispatch] = React.useReducer(reducer, defaultState);

  const addToast = React.useCallback(
    (payload: ToastInfo | string) => {
      const info: ToastInfo = typeof payload === "string" ? { message: payload, variant: "info" } : payload;
      const toastItem = createToast(info);

      dispatch({
        type: ActionTypes.ADD_TOAST,
        payload: toastItem,
      });
    },
    [dispatch]
  );

  const removeToast = React.useCallback(
    (id: string) => {
      dispatch({
        type: ActionTypes.REMOVE_TOAST,
        payload: id,
      });
    },
    [dispatch]
  );

  const registerHeight = React.useCallback(
    (id: string, height: number) => {
      if (!state.heights[id]) {
        dispatch({
          type: ActionTypes.REGISTER_HEIGHT,
          payload: { id, height },
        });
      }
    },
    [dispatch, state.heights]
  );

  const transitions = useTransition(state.toasts, {
    from: {
      transform: "translateX(110%)",
    },
    enter: {
      transform: "translateX(0%)",
    },
    leave: {
      transform: "translateX(110%)",
    },
  });

  return (
    <Provider value={{ addToast, removeToast, registerHeight }}>
      {children}
      <ToastContainer portalId={portalId}>
        {transitions((transition, toast) => {
          const keys = Object.keys(state.heights);
          const toastIndex = keys.indexOf(toast.id);
          const stackKeys = toastIndex === -1 ? keys : keys.slice(0, toastIndex);
          const stackHeight = Object.entries(state.heights).reduce((value, entry) => {
            if (stackKeys.includes(entry[0])) {
              value += entry[1];
            }
            return value;
          }, 0);
          return (
            <Toast
              key={toast.id}
              transition={transition}
              removeToast={removeToast}
              stackHeight={stackHeight}
              removed={toastIndex === -1}
              combination={toast.combine}
              combinations={toast.combinationToasts}
              {...toast}
            />
          );
        })}
      </ToastContainer>
    </Provider>
  );
}

export function useToast(): IToastContext {
  const context = React.useContext(ToastContext);

  if (!context) {
    throw new Error("useToast must be a child of ToastProvider");
  }

  return context;
}
