import * as React from "react";
import classNames from "classnames";

import styles from "./IconButton.module.css";
import skeletonStyles from "../../theme/skeleton.module.css";

export interface IconButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  /**
   * Specify an optional className to be applied to the container node
   */
  className?: string;
  /**
   * Specify a thin circle border
   */
  hasBorder?: boolean;
  /**
   * Skeleton mode
   */
  isSkeleton?: boolean;
  /**
   * Accessiblity label to identify button
   */
  label: string;
}

export const IconButton = React.forwardRef<HTMLButtonElement, IconButtonProps>(function IconButton(props, ref) {
  const { onClick, className, children, hasBorder, isSkeleton, disabled, label, ...otherProps } = props;
  const isDisabled = disabled || isSkeleton;
  return (
    <button
      ref={ref}
      onClick={onClick}
      disabled={isDisabled}
      className={classNames(styles.container, className, {
        [styles.border]: hasBorder,
        [skeletonStyles.skeleton]: isSkeleton,
        [styles.skeleton]: isSkeleton,
      })}
      aria-busy={isSkeleton}
      aria-label={label}
      {...otherProps}
    >
      {children}
    </button>
  );
});
