import * as React from "react";
import classNames from "classnames";
import styles from "./TextInput.module.css";
import skeletonStyles from "../../theme/skeleton.module.css";
import { Typography } from "../Typography";
import { CharacterCount } from "./CharacterCount";
import useCombinedRefs from "../../hooks/useCombinedRefs";

type TextInputVariant = "default" | "small";

export interface TextInputProps extends React.ComponentPropsWithRef<"input"> {
  /**
   * Specify an optional className to be applied to the container node
   */
  className?: string;
  /**
   * Specify an optional className to be applied to the label node
   */
  labelClassName?: string;
  /**
   * Specify an optional className to be applied to the input node
   */
  inputClassName?: string;
  /**
   * Provide an optional label
   */
  label?: string;
  /**
   * Set input to error state
   */
  error?: boolean;
  /**
   * Specify an error message to display next to input
   */
  errorMessage?: string;
  /**
   * Icon component aligned to right
   */
  iconRight?: React.ReactNode;
  /**
   * Specify an optional className to be applied to the right icon
   */
  iconRightClassName?: string;
  /**
   * Icon component aligned to left
   */
  iconLeft?: React.ReactNode;
  /**
   * Specify an optional className to be applied to the left icon
   */
  iconLeftClassName?: string;
  /**
   * Click handler for icon
   */
  onClickIcon?: () => void;
  /**
   * Optional variant
   */
  variant?: TextInputVariant;
  /**
   * Skeleton mode
   */
  isSkeleton?: boolean;
  /**
   * Show character count
   */
  showCharacterCount?: boolean;
  /**
   * Alignment of character counter
   */
  characterCountAlign?: "left" | "right";
  /**
   * Focus the input box on control click
   */
  focusOnControlClick?: boolean;
}

export const TextInput = React.forwardRef<HTMLInputElement, TextInputProps>(function TextInput(props, ref) {
  const {
    className,
    labelClassName,
    inputClassName,
    iconLeftClassName,
    iconRightClassName,
    label,
    id,
    iconRight,
    iconLeft,
    error,
    errorMessage,
    onClickIcon,
    variant = "default",
    isSkeleton,
    disabled,
    showCharacterCount,
    characterCountAlign = "right",
    focusOnControlClick,
    ...inputProps
  } = props;

  const innerRef = React.useRef(null);
  const textInputRef = useCombinedRefs(ref, innerRef);
  const controlRef = React.useRef<HTMLDivElement>(null);

  const isDisabled = disabled || isSkeleton;
  const isErrorMessage = error && errorMessage !== undefined;
  const isCharacterCount = showCharacterCount && inputProps.maxLength !== 0;

  const onControlClick = React.useCallback(() => {
    if (!isDisabled && focusOnControlClick) textInputRef.current?.focus();
  }, [focusOnControlClick, isDisabled, textInputRef]);

  return (
    <div
      aria-busy={isSkeleton}
      className={classNames(styles.group, className, {
        [styles.small]: variant === "small",
        [styles.skeleton]: isSkeleton,
      })}
      onClick={onControlClick}
      ref={controlRef}
    >
      {isErrorMessage && (
        <Typography role="alert" variant="small" className={styles.errorMessage}>
          {errorMessage}
        </Typography>
      )}
      {label && (
        <label
          htmlFor={id}
          className={classNames(styles.label, labelClassName, {
            [skeletonStyles.skeleton]: isSkeleton,
            [styles.skeletonLabel]: isSkeleton,
          })}
        >
          <span className={styles.labelText}>{label}</span>
        </label>
      )}
      <div className={classNames(styles.wrapper)}>
        <input
          name={id}
          ref={textInputRef}
          id={id}
          type="text"
          className={classNames(
            styles.input,
            {
              [styles.error]: error,
              [styles.leftPad]: !!iconLeft,
              [skeletonStyles.skeleton]: isSkeleton,
              [styles.skeletonInput]: isSkeleton,
            },
            inputClassName
          )}
          disabled={isDisabled}
          aria-hidden={isSkeleton ? "true" : undefined}
          {...inputProps}
        />
        {iconLeft && (
          <span
            data-testid="text-input-icon-left"
            onClick={onClickIcon}
            className={classNames(styles.icon, styles.left, iconLeftClassName)}
          >
            {iconLeft}
          </span>
        )}
        {iconRight && (
          <span
            data-testid="text-input-icon-right"
            onClick={onClickIcon}
            className={classNames(styles.icon, styles.right, iconRightClassName)}
          >
            {iconRight}
          </span>
        )}
      </div>
      {isCharacterCount && (
        <div
          className={classNames(styles.characterCount, {
            [styles.alignRight]: characterCountAlign === "right",
            [skeletonStyles.skeleton]: isSkeleton,
            [styles.characterCountSkeleton]: isSkeleton,
          })}
        >
          <CharacterCount
            maxLen={inputProps.maxLength}
            valueLen={inputProps.value?.toString().length}
            isSkeleton={isSkeleton}
          />
        </div>
      )}
    </div>
  );
});
