import * as React from "react";
import classNames from "classnames";
import { useSelect } from "downshift";
import { ChevronDownIcon } from "../../icons";
import { Options } from "@popperjs/core";
import inputStyles from "../TextInput/TextInput.module.css";
import skeletonStyles from "../../theme/skeleton.module.css";
import styles from "./SelectInput.module.css";
import { Popover } from "../Popover";
import { Typography } from "../Typography";
import { PopoverSize } from "../Popover/Popover";
import useContentScrolls from "../../hooks/useContentScrolls";

export type SelectInputVariant = "default" | "small";

export type SelectInputValue = { label: string; value: string };

export interface SharedSelectProps {
  className?: string;
  placeholder?: string;
  isSkeleton?: boolean;
  popperOptions?: Partial<Options>;
  popoverSize?: PopoverSize;
}

export interface SelectInputProps extends React.ComponentPropsWithRef<"button">, SharedSelectProps {
  items: SelectInputValue[];
  labelClassName?: string;
  label?: string;
  /**
   * Set input to error state
   */
  error?: boolean;
  /**
   * Specify an error message to display next to input
   */
  errorMessage?: string;
  initialSelectedItem?: SelectInputValue;
  variant?: SelectInputVariant;
  handleSelectedItemChange?: (selectedItem: SelectInputValue | null | undefined) => void;
  "data-testid"?: string;
}

export const SelectInput = React.forwardRef<HTMLButtonElement, SelectInputProps>(function SelectInput(props, ref) {
  const {
    items,
    className,
    labelClassName,
    label,
    id,
    error,
    errorMessage,
    variant = "default",
    popoverSize = "full",
    placeholder,
    initialSelectedItem,
    handleSelectedItemChange,
    isSkeleton,
    disabled,
    popperOptions = {
      modifiers: [
        {
          name: "offset",
          options: {
            offset: [0, 2],
          },
        },
      ],
    },
    ...buttonProps
  } = props;

  const {
    isOpen,
    closeMenu,
    selectedItem,
    getToggleButtonProps,
    getLabelProps,
    getMenuProps,
    highlightedIndex,
    getItemProps,
  } = useSelect<SelectInputValue | null>({
    items,
    initialSelectedItem,
    onSelectedItemChange: ({ selectedItem: selected }) => {
      handleSelectedItemChange && handleSelectedItemChange(selected);
    },
  });

  const [referenceElement, setReferenceElement] = React.useState<HTMLElement | null>(null);
  const { contentScrolls } = useContentScrolls(getMenuProps().ref);

  const isDisabled = disabled || isSkeleton;
  const isErrorMessage = error && errorMessage !== undefined;

  return (
    <div
      aria-busy={isSkeleton}
      className={classNames(styles.container, { [inputStyles.group]: variant === "default" }, className)}
    >
      {isErrorMessage && (
        <Typography variant="small" className={inputStyles.errorMessage} role="alert">
          {errorMessage}
        </Typography>
      )}
      {label && (
        <label
          htmlFor={id}
          className={classNames(inputStyles.label, labelClassName, {
            [skeletonStyles.skeleton]: isSkeleton,
            [styles.skeletonLabel]: isSkeleton,
          })}
          {...getLabelProps()}
        >
          <span className={styles.labelText}>{label}</span>
        </label>
      )}
      <div className={classNames(inputStyles.wrapper)}>
        <button
          type="button"
          ref={ref}
          id={id}
          className={classNames(inputStyles.input, styles.button, {
            [inputStyles.error]: error,
            [styles.small]: variant === "small",
            [skeletonStyles.skeleton]: isSkeleton,
            [styles.skeletonButton]: isSkeleton,
          })}
          {...getToggleButtonProps({ ref: setReferenceElement })}
          disabled={isDisabled ? "disabled" : undefined}
          {...buttonProps}
        >
          <span className={styles.buttonLabel}>{selectedItem?.label || placeholder}</span>
          <span className={styles.icon}>
            <ChevronDownIcon />
          </span>
        </button>
        <Popover
          className={styles.selectInputPopper}
          popoverClassName={styles.selectInputPopover}
          isOpen={isOpen}
          anchorElement={referenceElement || undefined}
          onClose={closeMenu}
          popperOptions={{ placement: "bottom", ...popperOptions }}
          size={popoverSize === "fixed" && contentScrolls ? "large" : popoverSize}
        >
          <ul className={styles.menu} {...getMenuProps()}>
            {items.map((item, index) => (
              <li
                className={classNames(
                  styles.item,
                  { [styles.highlight]: highlightedIndex === index },
                  {
                    [styles.selected]: selectedItem?.value === item.value,
                  }
                )}
                key={`${item}${index}`}
                {...getItemProps({ item, index })}
              >
                {item.label}
              </li>
            ))}
          </ul>
        </Popover>
      </div>
    </div>
  );
});
