import * as React from "react";
import classNames from "classnames";
import styles from "./DataTable.module.css";
import { ColumnDefinition, DataDefinition, RowInfo } from "./types";

interface TableBodyProps<T extends DataDefinition[], K> {
  /**
   * Row data
   */
  rowsInfo: RowInfo<T>[];
  /**
   * Column definitions (accepts generic key type)
   */
  columnDefs: ColumnDefinition<K>[];
}

function createRow<T extends DataDefinition[], K>(rowData: T, columnDefs: ColumnDefinition<K>[]) {
  return columnDefs.map((def, index) => {
    const { component: Component } = def;
    const item = rowData[index];
    return <Component key={index} {...item} />;
  });
}

export default function TableBody<T extends DataDefinition[], K>(props: TableBodyProps<T, K>): JSX.Element {
  const { rowsInfo, columnDefs } = props;
  return (
    <tbody>
      {rowsInfo.map((row, index) => {
        const cells = createRow<T, K>(row.data, columnDefs);
        return (
          <tr
            key={index}
            className={classNames(styles.row, { [styles.clickable]: !!row.onClick })}
            onClick={row.onClick}
          >
            {cells.map((item) => (
              <td key={item.key} className={styles.cell}>
                {item}
              </td>
            ))}
          </tr>
        );
      })}
    </tbody>
  );
}
