import * as React from "react";
import classNames from "classnames";

import styles from "./ClipboardInput.module.css";
import { TextInput } from "../TextInput";
import { ClipboardCopy } from "../ClipboardCopy";
import type { ClipboardCopyProps } from "../ClipboardCopy";

export interface ClipboardInputProps extends Pick<ClipboardCopyProps, "onCopy" | "onError"> {
  className?: string;
  text?: string;
  /**
   * Skeleton mode
   */
  isSkeleton?: boolean;
}

export const ClipboardInput = React.forwardRef<HTMLDivElement, ClipboardInputProps>(function ClipboardInput(
  props,
  ref
) {
  const { className, text = "", onCopy, onError, isSkeleton, ...otherProps } = props;
  return (
    <div ref={ref} className={classNames(styles.container, className)}>
      <TextInput
        isSkeleton={isSkeleton}
        className={styles.textContainer}
        inputClassName={styles.textInput}
        value={text}
        variant="small"
        readOnly
        {...otherProps}
      />
      <ClipboardCopy isSkeleton={isSkeleton} text={text} onCopy={onCopy} onError={onError} />
    </div>
  );
});
