import * as React from "react";
import classNames from "classnames";

import styles from "./TextArea.module.css";
import textInputStyles from "../TextInput/TextInput.module.css";
import skeletonStyles from "../../theme/skeleton.module.css";

import { Typography } from "../Typography";
import { CharacterCount } from "../TextInput/CharacterCount";

type TextAreaProps = {
  /**
   * Specify a class to override container styles
   */
  containerClassName?: string;
  /**
   * Specify a class to override label styles
   */
  labelClassName?: string;
  /**
   * Specify a label for the textarea
   */
  label?: string;
  /**
   * Set input to error state
   */
  error?: boolean;
  /**
   * Specify an error message to display next to input
   */
  errorMessage?: string;
  /**
   * Skeleton mode
   */
  isSkeleton?: boolean;
  /**
   * Show character count
   */
  showCharacterCount?: boolean;
  /**
   * Alignment of character counter
   */
  characterCountAlign?: "left" | "right";
};

export type Props = React.TextareaHTMLAttributes<HTMLTextAreaElement> & TextAreaProps;

export const TextArea = React.forwardRef<HTMLTextAreaElement, Props>(function TextArea(
  {
    id,
    label,
    containerClassName,
    className,
    labelClassName,
    value,
    onChange,
    error,
    errorMessage,
    isSkeleton,
    showCharacterCount,
    characterCountAlign = "right",
    ...textAreaProps
  },
  ref
) {
  const isErrorMessage = error && errorMessage !== undefined;
  const isCharacterCount = showCharacterCount && textAreaProps.maxLength !== 0;
  return (
    <div aria-busy={isSkeleton} className={classNames(styles.container, textInputStyles.group, containerClassName)}>
      {isErrorMessage && (
        <Typography role="alert" variant="small" className={textInputStyles.errorMessage}>
          {errorMessage}
        </Typography>
      )}
      {label && (
        <label
          htmlFor={id}
          className={classNames(textInputStyles.label, labelClassName, {
            [skeletonStyles.skeleton]: isSkeleton,
            [styles.skeletonLabel]: isSkeleton,
          })}
        >
          <span className={textInputStyles.labelText}>{label}</span>
        </label>
      )}
      <textarea
        className={classNames(
          textInputStyles.control,
          styles.textarea,
          {
            [textInputStyles.error]: error,
            [skeletonStyles.skeleton]: isSkeleton,
            [styles.skeletonTextarea]: isSkeleton,
          },
          className
        )}
        id={id}
        ref={ref}
        value={value}
        onChange={onChange}
        aria-hidden={isSkeleton ? "true" : undefined}
        {...textAreaProps}
      />
      {isCharacterCount && (
        <div
          className={classNames(textInputStyles.characterCount, {
            [textInputStyles.alignRight]: characterCountAlign === "right",
            [skeletonStyles.skeleton]: isSkeleton,
            [textInputStyles.characterCountSkeleton]: isSkeleton,
          })}
        >
          <CharacterCount
            maxLen={textAreaProps.maxLength}
            valueLen={value?.toString().length}
            isSkeleton={isSkeleton}
          />
        </div>
      )}
    </div>
  );
});
