import * as React from "react";
import classNames from "classnames";

import styles from "./CodeBlock.module.css";
import { Typography } from "../Typography";

export interface CodeBlockProps extends React.HTMLAttributes<HTMLDivElement> {
  title: string;
  text: string;
  className?: string;
}

export const CodeBlock = React.forwardRef<HTMLDivElement, CodeBlockProps>(function CodeBlock(props, ref) {
  const { title, text, className, ...otherProps } = props;
  return (
    <div ref={ref} className={classNames(styles.container, className)} {...otherProps}>
      <div className={styles.header}>
        <Typography className={styles.title} variant="h3">{title}</Typography>
      </div>
      <div className={styles.body}>
        <Typography className={styles.text}>{text}</Typography>
      </div>
    </div>
  )
});
