import * as React from "react";
import classNames from "classnames";
import { animated, useTransition } from "react-spring";
import usePortal from "react-useportal";
import { ResizeObserver } from "@juggle/resize-observer";

import styles from "./Modal.module.css";
import { useOrchard } from "../OrchardProvider";

export interface ModalProps {
  className?: string;
  isHideBackgroundOverlay?: boolean;
  isPinned?: boolean;
  onClose: (event?: React.MouseEvent<HTMLElement>) => void;
}

const noScrollClassName = "orc-modal--noscroll";

export function measureScrollBarWidth(entries: ResizeObserverEntry[], isOpen: boolean): number | undefined {
  let result;
  // Skip scroll bar width measurement if modal is open
  if (isOpen) return;
  // Measure scroll bar width
  for (const entry of entries) {
    const measuredWidth = self.innerWidth - entry.borderBoxSize[0].inlineSize;
    const cs = window.getComputedStyle(entry.target);
    const paddingRight = parseInt(cs.paddingRight, 10);
    result = measuredWidth;
    if (!isNaN(paddingRight)) {
      result += paddingRight;
    }
  }
  return result;
}

export function Modal({
  className,
  children,
  isHideBackgroundOverlay,
  isPinned,
  onClose,
  ...otherProps
}: React.PropsWithChildren<ModalProps>): JSX.Element {
  const [scrollBarWidth, setScrollBarWidth] = React.useState(0);
  const { modalPortalId } = useOrchard();

  const { Portal } = usePortal({ bindTo: (document && document.getElementById(modalPortalId)) || undefined });

  const child = children ? React.Children.only(children) : null;

  const isOpen = Boolean(child);

  const removeBodyStyle = () => {
    document.body.classList.remove(styles.noscroll);
    document.body.classList.remove(noScrollClassName);
    document.body.style.removeProperty("padding-right");
  };

  const handleRest = React.useCallback(() => !isOpen && removeBodyStyle(), [isOpen]);

  const transition = useTransition(children, {
    key: (item: any) => item?.key,
    from: {
      opacity: 0,
    },
    enter: {
      opacity: 1,
    },
    leave: {
      opacity: 0,
      position: "absolute",
      left: 0,
      top: 0,
      overflow: "hidden",
    },
    onRest: handleRest
  });

  React.useEffect(() => {
    const resizeObserver = new ResizeObserver((entries) => {
      const scrollBarWidthResult = measureScrollBarWidth(entries, isOpen);
      if (scrollBarWidthResult) {
        setScrollBarWidth(scrollBarWidthResult);
      }
    });

    resizeObserver.observe(document.body);

    return () => {
      resizeObserver.unobserve(document.body);
    };
  }, [isOpen]);

  // On unmount
  React.useEffect(() => () => removeBodyStyle(), []);

  React.useEffect(() => {
    if (isOpen) {
      document.body.classList.add(styles.noscroll);
      document.body.classList.add(noScrollClassName);
      document.body.style.paddingRight = `${scrollBarWidth}px`;
    }
  }, [isOpen, scrollBarWidth]);

  const handleMouseDown = (event: React.MouseEvent<HTMLElement>) => {
    if (event.target === event.currentTarget) {
      onClose(event);
    }
  };

  return (
    <Portal>
      <div style={{ pointerEvents: isOpen ? "auto" : "none" }} className={classNames(styles.modal, className)}>
        {transition(
          (style, item) =>
            item && (
              <animated.div
                role="document"
                className={classNames(
                  styles.content,
                  { [styles.transparent]: isHideBackgroundOverlay },
                  { [styles.pinned]: isPinned }
                )}
                style={style}
                onMouseDown={handleMouseDown}
                {...otherProps}
              >
                {item}
              </animated.div>
            )
        )}
      </div>
    </Portal>
  );
}
