import * as React from "react";

type CharacterCountProps = {
  maxLen?: number;
  valueLen?: number;
  isSkeleton?: boolean;
};

export const CharacterCount: React.VFC<CharacterCountProps> = (props) => {
  const { maxLen, valueLen, isSkeleton } = props;

  if (isSkeleton) {
    return <span>&nbsp;</span>;
  }

  if (!maxLen || !valueLen) {
    return <span>- characters remaining</span>;
  }

  return (
    <span>
      {maxLen - valueLen}/{maxLen} characters remaining
    </span>
  );
};
